@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

$btnProps: (
		'border-radius': 6px,
		'border-radius-sm': 4px,
		'height-lg': 50px,
		'height-md': 44px,
		'height-sm': 38px,
		'height-xs': 30px,
		'height-xxs': 24px,
		'height-ip': 44px,
		'padding': 0 23px 1px,
		'padding-sm': 0 13px 1px,
		'padding-xs': 0 9px 1px,
		'padding-xxs': 1px 5px 1px,
		'padding-ip': 0 19px 1px,
		'min-width-lg': 200px,
		'min-width-md': 140px,
		'min-width-sm': 86px,
);

/* 버튼 */
button[class^=btn_], a[class^=btn_] {
	display: inline-block;
	box-sizing: border-box;
	vertical-align: top;

	// 이벤트/비활성화
	&:hover {
		cursor: pointer;
	}

	&[disabled], &[aria-disabled], &.disabled {
		pointer-events: none;
		cursor: default;
	}

	// 아이콘
	[class^=ico_] {
		display: inline-block;
		position: relative;
		vertical-align: top;
	}

	// 텍스트 - ie 딸각거림 방지를 위해 필요
	.text {
		display: inline-block;
		position: relative;
		vertical-align: top;
	}
}

/* 버튼 - 사이즈 */
a.btn_lg, button.btn_lg {
	@include mixins.justify_width(center, center, row, true);
	min-width: map.get($btnProps, 'min-width-lg');
