		@include mixins.ratio_images('../images/ico_alarm_off', 'png', 14px, 14px);
	}

	.text {
		position: relative;
		padding-right: 1px;
	}

	&.active {
		color: variables.$kyoboPurple;
		border-color: variables.$kyoboPurple;

		.ico_alarm {
			background: {
				position: 1px center;
			}
			@include mixins.ratio_images('../images/ico_alarm_on', 'png', 12px, 18px);
		}
	}
}

/* 구독 on/off 버튼 */
a.btn_subscribe, button.btn_subscribe {
	min-width: auto;
	border: 1px solid variables.$colorGray02;

	.ico_subscribe {
		display: inline-block;
		width: 14px;
		height: 14px;
		margin: {
			top: 2px;
			right: 4px;
		}
		background: {
			position: center;
			repeat: no-repeat;
		}
		font-size: 0;
		@include mixins.ratio_images('../images/ico_subscribe', 'png', 14px, 14px);
	}

	.text {
		font-weight: variables.$fontWeightMedium;
	}

	&.active {
		color: #fff;
		border-color: variables.$kyoboGreen;
		background: variables.$kyoboGreen;

		.ico_subscribe {
			@include mixins.ratio_images('../images/ico_subscribe_check_w', 'png', 14px, 14px);
		}
	}

	&.btn_sm {
		.ico_subscribe {
			display: inline-block;
			width: 12px;
			height: 12px;
			margin: {
				//top: 4px;
				//right: 4px;
			}
			background: {
				position: center;
				repeat: no-repeat;
			}
			font-size: 0;
			@include mixins.ratio_images('../images/ico_subscribe_sm', 'png', 12px, 12px);
		}

		.text {
			font-weight: variables.$fontWeightRegular;
		}

		&.active {
			.ico_subscribe {
				@include mixins.ratio_images('../images/ico_subscribe_check_sm_w', 'png', 12px, 12px);
			}
		}
	}

	&.white_type {
		color: #fff;
		border-color: rgba(255, 255, 255, .5);

		.ico_subscribe {
			@include mixins.ratio_images('../images/ico_subscribe_w', 'png', 14px, 14px);
		}
		&.active {
			border-color: variables.$kyoboGreen;

			.ico_subscribe {
				@include mixins.ratio_images('../images/ico_subscribe_check_w', 'png', 14px, 14px);
			}
		}
	}
}

/* 버튼 - 링크 텍스트 */
a.btn_text_link, button.btn_text_link {
	color: variables.$textColorLightGray;
	@include mixins.font_size_xs;
	font-weight: variables.$fontWeightRegular;
	text-align: left;

	.text {
		//text-decoration: underline;
	}

	&.ellipsis {
		max-width: 100%;

		.text {
			@include mixins.ellipsis(false);
		}
	}

	&.row_2_ellipsis {
		.text {
			@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 2, false);
		}
	}

	&.size_lg {
		@include mixins.font_size_sm;
		font-weight: variables.$fontWeightMedium;

		&.row_2_ellipsis {
			.text {
				@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight, 2, false);
			}
		}
	}

	&.size_sm {
		@include mixins.font_size_xxs;

		&.row_2_ellipsis {
			.text {
				@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 2, false);
			}
		}
	}

	[class^=ico_] {
		margin-left: 4px;
	}

	&:hover, &:focus, &:active {
		.text {
			text-decoration: underline;
		}
	}
}

