/* 버튼 정렬 */
.btn_wrap {
	display: block;
	font-size: 0;
	text-align: center;

	[class^=btn_] {
		& + [class^=btn_] {
			margin-left: 6px;
		}
	}

	.btn_lg {
		& + .btn_lg {
			margin-left: 10px;
		}
	}

	&.page_bottom { // todo : 디자인 수급 시 수정
		margin-top: 50px;
	}

	&.justify {
		display: flex;

		[class^=btn_] {
			flex: 1;
			min-width: auto;

			& + [class^=btn_] {
				margin-left: 6px;
			}
		}
	}

	&.full {
		[class^=btn_] {
			width: 100%;
			min-width: auto;

			& + [class^=btn_] {
				margin: 8px 0 0;
			}
		}
	}

	&.overlap {
		.btn_lg, .btn_sm {
			&:not(:only-child) {
				border-radius: 0;

				&:first-child {
					border-radius: 6px 0 0 6px;
				}

				&:last-child {
					border-radius: 0 6px 6px 0;
				}
			}
		}

		.btn_lg {
			+ .btn_lg {
				margin-left: -1px;
			}
		}

		.btn_sm {
			+ .btn_sm {
				margin-left: -1px;
			}
		}

		&.single {
			border-radius: 6px;
		}
	}
}

