@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/*===== 상품 섬네일 아이템 =====*/

.thumbnail_product_item {
	position: relative;

	&:after {
		content: '전시 템플릿 정의 후 작업';
		position: absolute;
		display: inline-block;
		color: #fff;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background-color: rgba(255, 0, 0, .5);
		pointer-events: none;
	}

	// 상품 섬네일 세로형
	&.vertical_type {
		width: 134px;

		.thumbnail_area {
			display: block;
			overflow: hidden;
			width: 100%;
			min-height: 10px;
			box-sizing: border-box;
			border: 1px solid variables.$colorGray04;
			background-color: variables.$colorGray04;

			.link_thumbnail {
				display: block;
				width: 100%;

				img {
					display: block;
					width: 100%;
				}
			}
		}

		.info_area {
			display: block;
			width: 100%;
			margin-top: 16px;
			box-sizing: border-box;

			&.align_center {
				margin-top: 10px;
				text-align: center;

				[class^='info_'] {
					text-align: center;
				}

				.info_product_name {
					@include mixins.font_size_xxs;
					@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 2, false);

					.link_text {
						@include mixins.font_size_xxs;
						text-align: center;
					}
				}
			}

			.info_product_label {
				display: block;
				color: variables.$textColorGray;
				@include mixins.font_size_xs;
				margin-bottom: 5px;
			}

			.review_quotes_text {
				margin-top: 20px;
			}

			.info_product_name {
				@include mixins.font_size_sm;
				//font-weight: variables.$fontWeightMedium;
				color: variables.$textColorBlack;
				@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight, 2, false);

				.link_text {
					display: block;
					width: 100%;
					@include mixins.font_size_sm;
					//font-weight: variables.$fontWeightMedium;
					color: variables.$textColorBlack;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(23px, 2, false);
					}
				}
			}

			.info_author_publication {
				margin-top: 5px;

				.info_author {
					display: inline-block;
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(19px, 2, false);
					}
				}

				.info_publication {
					display: inline-block;
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(19px, 2, false);
					}
				}

				[class^='info_'] + [class^='info_'] {
					position: relative;
					padding-left: 6px;

					&:before {
						content: '';
						position: absolute;
						left: 0;
						top: 8px;
						display: block;
						width: 2px;
						height: 2px;
						border-radius: 50%;
						background-color: variables.$textColorGray;
					}
				}
			}

			.info_price {
				margin-top: 12px;

				[class^='price_'] {
					display: inline-block;
					@include mixins.font_size_xs;
				}

				.price_dc {
					font-weight: variables.$fontWeightBold;
					color: variables.$textColorGreen;
					font-family: variables.$fontEng;
				}

				.price_amount {
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorBlack;

					strong {
						font-weight: variables.$fontWeightBold;
						color: variables.$textColorBlack;
						font-family: variables.$fontEng;
					}
				}

				.price_point {
					@include mixins.font_size_min;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;
					font-family: variables.$fontEng;
				}
			}
		}

		.product_check {
			margin-bottom: 6px;
		}
	}

	/* 상품 섬네일 가로형 */
	&.horizontal_type {
		@include mixins.justify_width(center, start, row);
		width: auto;
		min-height: 10px;

		@at-root .thumbnail_list_horizontal & {
			min-width: 250px;
		}

		.thumbnail_area {
			display: block;
			overflow: hidden;
			width: 84px;
			min-height: 10px;
			box-sizing: border-box;
			border: 1px solid variables.$colorGray04;
			background-color: variables.$colorGray04;
			flex-shrink: 0;

			.link_thumbnail {
				display: block;
				width: 100%;

				img {
					display: block;
					width: 100%;
				}
			}
		}

		.info_area {
			display: block;
			width: auto;
			box-sizing: border-box;
			padding-left: 12px;

			.info_product_number {
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightRegular;
				font-family: variables.$fontEng;
				color: variables.$textColorGray;
			}

			.info_product_label {
				display: block;
				color: variables.$textColorGray;
				@include mixins.font_size_xs;
				margin-bottom: 5px;
			}

			.review_quotes_text {
				margin-top: 20px;
			}

			.info_product_name {
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightMedium;
				color: variables.$textColorBlack;
				@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 2, false);

				.link_text {
					display: block;
					width: 100%;
					@include mixins.font_size_xs;
					font-weight: variables.$fontWeightMedium;
					color: variables.$textColorBlack;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(23px, 2, false);
					}
				}
			}

			.info_author_publication {
				margin-top: 5px;

				.info_author {
					display: inline-block;
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(19px, 2, false);
					}
				}

				.info_publication {
					display: inline-block;
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;

					&.multi_ellipsis {
						@include mixins.multi_ellipsis(19px, 2, false);
					}
				}

				[class^='info_'] + [class^='info_'] {
					position: relative;
					padding-left: 6px;

					&:before {
						content: '';
						position: absolute;
						left: 0;
						top: 8px;
						display: block;
						width: 2px;
						height: 2px;
						border-radius: 50%;
						background-color: variables.$textColorGray;
					}
				}
			}

			.info_price {
				margin-top: 20px;

				[class^='price_'] {
					display: inline-block;
					@include mixins.font_size_xs;
				}

				.price_dc {
					font-weight: variables.$fontWeightBold;
					color: variables.$textColorGreen;
					font-family: variables.$fontEng;
				}

				.price_amount {
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorBlack;

					strong {
						font-weight: variables.$fontWeightBold;
						color: variables.$textColorBlack;
						font-family: variables.$fontEng;
					}
				}

				.price_point {
					@include mixins.font_size_min;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;
					font-family: variables.$fontEng;
				}
			}

			.rating-container {
				margin-top: 18px;
			}
		}
	}

	/* 섬네일 사이즈 별 */
	&.vertical_type {
		// 스타일 가이드 common guide 기준. Medium Size로 잡고 default value로 지정
		&.width_md {
			// width: 134px;
			.info_area {
				// margin-top: 16px;
				&.align_center {
					// margin-top: 10px;
				}

				.info_author_name {
					// margin-top: 5px;
				}

				.info_price {
					// margin-top: 12px;
				}
			}
		}

		&.width_sm {
			width: 122px;

			.info_area {
				margin-top: 16px;

				&.align_center {
					margin-top: 10px;
				}

				.info_author_name {
					margin-top: 5px;
				}

				.info_price {
					margin-top: 12px;
				}
			}
		}
	}

}

/* 섬네일 리스트 */
[class*='thumbnail_list'] {
	@include mixins.justify_width(start, start, row);
	box-sizing: border-box;

	&.row_stack {
		flex-wrap: wrap;
	}

	/* Vertical Type List */
	&.thumbnail_list_vertical {
		margin: -10px;

		.thumbnail_item {
			padding: 10px;
			box-sizing: border-box;
		}
	}

	/* Horizontal Type List */
	&.thumbnail_list_horizontal {
		margin: -8px;

		.thumbnail_item {
			padding: 8px;
			box-sizing: border-box;

			&:first-child {
				.thumbnail_product_item.horizontal_type {
					.info_area {
						.info_product_number {
							font-weight: variables.$fontWeightMedium;
							color: variables.$textColorGreen;
						}
					}
				}
			}
		}

		&.row_stack {
			min-width: auto;

			.thumbnail_item {
				.thumbnail_product_item {
					min-width: auto;
				}
			}
		}

		&.full_width {
			flex-wrap: wrap;

			.thumbnail_item {
				width: 100%;
				min-width: 100%;
			}
		}

		&.align_top {
			.thumbnail_item {
				.thumbnail_product_item {
					&.horizontal_type {
						@include mixins.justify_width(start, start, row);

						.info_area {
							padding-top: 10px;
						}
					}
				}
			}
		}
	}
}
.swiper-container {
	&.scrollbar_text {
		.swiper-wrapper.thumbnail_list_vertical {
			padding-bottom: 20px;

			.thumbnail_item.swiper-slide {
				width: auto;
			}
		}

		.swiper-wrapper {
			.swiper-slide {
				.thumbnail_list_vertical {
					.thumbnail_item {
						width: auto;
					}
				}
			}
		}

		&.view_type_img {
			.swiper-wrapper.thumbnail_list_vertical {

			}
		}

		&.view_type_txt {
			.swiper-wrapper.thumbnail_list_vertical {

			}
		}

		&.view_type_list {

		}
	}
}


/* 상품 리스 */
.prod_list {
	font-size: 0;

	.prod_item {
		display: inline-block;
		width: calc((100% - 144px) / 5);
		margin-left: 36px;
		vertical-align: top;

		&:nth-child(5n+1) {
			margin-left: 0;
		}

		&:nth-child(n+6) {
			margin-top: 60px;
		}
	}

	&.col4 {
		.prod_item {
			width: calc((100% - 108px) / 4);

			&:nth-child(5n+1) {
				margin-left: 36px;
			}

			&:nth-child(4n+1) {
				margin-left: 0;
			}

			&:nth-child(n+5) {
				margin-top: 60px;
			}
		}
	}

	&.col3 {
		.prod_item {
			width: calc((100% - 72px) / 3);

			&:nth-child(5n+1) {
				margin-left: 36px;
			}

			&:nth-child(3n+1) {
				margin-left: 0;
			}

			&:nth-child(n+4) {
				margin-top: 60px;
			}
		}
	}

	&.col2 {
		.prod_item {
			width: calc((100% - 36px) / 2);

			&:nth-child(5n+1) {
				margin-left: 36px;
			}

			&:nth-child(2n+1) {
				margin-left: 0;
			}

			&:nth-child(n+3) {
				margin-top: 60px;
			}
		}
	}

	&.swiper-wrapper {
		.prod_item {
			margin-top: 0 !important;
		}
	}
}

/* 상품 아이템 */
.prod_area {
	.prod_thumb_box {
		position: relative;
		overflow: hidden;
		border: 1px solid variables.$colorGray04;
		box-sizing: border-box;

		.prod_link {
			display: block;
			height: 100%;
		}

		img {
			width: 100%;
		}

		.img_box {
			display: block;

			img {
				width: 100%;
			}
		}

		.btn_wish {
			display: block;
			position: absolute;
			right: 7px;
			bottom: 7px;
			width: 30px;
			height: 30px;
			border: none;
			background: {
				repeat: no-repeat;
				position: center;
			}
			background-color: transparent;
			@include mixins.ratio_images('../images/ico_cast_heart_h15', 'png', 20px, 20px);

			&.active {
				@include mixins.ratio_images('../images/ico_cast_heart_h15_active', 'png', 20px, 20px);
			}
		}

		.thumb_flag_group {
			position: absolute;
			left: 0;
			top: 0;
			z-index: 2;

			.flag {
				display: inline-block;
				padding: 5px 10px;
				@include mixins.font_size_xxs;
				@include mixins.ellipsis(false);
				color: #fff;
				border-radius: 10px 0 16px 0;
				background-color: rgba(variables.$textColorBlack, .5);
				vertical-align: top;

				&.type_gray {
					background-color: rgba(variables.$textColorGray, .85);
				}

				&.type_green {
					background-color: rgba(variables.$kyoboGreen, .85);
				}
			}
		}

		&.type_cover {
			border-radius: 10px;
			border: none;

			.img_box {
				padding-top: 100%;

				background: {
					repeat: no-repeat;
					size: cover;
					position: center;
				}

				img {
					display: none;
				}
			}

		}

		&.type_contain {
			.img_box {
				padding-top: 100%;
				background: {
					repeat: no-repeat;
					size: contain;
					position: center;
				}

				img {
					display: none;
				}
			}
		}
	}

	.prod_info_box {
		.prod_link {
			display: block;
			height: 100%;
		}

		.prod_category {
			display: block;
			margin-bottom: 5px;
			@include mixins.font_size_xs;
			font-weight: variables.$fontWeightMedium;
			color: variables.$textColorPurple;
		}

		.prod_name {
			@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight);
			@include mixins.font_size_sm;
			font-weight: variables.$fontWeightMedium;

			&.h_auto {
				height: auto;
			}

			&.ellipsis_single {
				@include mixins.ellipsis;
			}
		}

		.prod_author {
			display: block;
			margin-top: 5px;
			@include mixins.font_size_xxs;
			color: variables.$textColorLightGray;
		}

		.prod_price {
			display: block;
			margin-top: 12px;
			font-size: 0;

			.percent {
				display: inline-block;
				margin-right: 5px;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightBold;
				color: variables.$kyoboGreen;
				vertical-align: top;
			}

			.price {
				display: inline-block;
				font-size: 0;
				vertical-align: top;

				.val, .unit {
					@include mixins.font_size_xs;
				}

				.val {
					font-weight: variables.$fontWeightBold;
				}

				.unit {

				}
			}
		}

		.prod_period {
			display: block;
			margin-top: 18px;
			@include mixins.font_size_xs;
			color: variables.$textColorLightGray;
		}

		.prod_tag {
			display: block;
			margin: 20px 0 0;
			font-size: 0;

			.btn_tag {
				display: inline-block;
				@include mixins.font_size_xs;
				vertical-align: middle;
				margin-right: 8px;

				&:last-child {
					margin-right: 0;
				}
			}
		}

		.prod_register {
			margin-top: 6px;

			span {
				position: relative;
				margin: 0 0 0 3px;
				padding: 0 0 0 7px;
				@include mixins.font_size_xs;
				vertical-align: top;

				&:after {
					display: block;
					content: "";
					position: absolute;
					left: 0;
					top: 10px;
					width: 2px;
					height: 2px;
					border-radius: 100%;
					background-color: #595959;
				}

				&:first-child {
					margin: 0;
					padding: 0;

					&:after {
						display: none;
					}
				}
			}

			.gap {
				display: none;
			}

			.date {
				display: inline-block;
				color: variables.$textColorLightGray;
			}

			.view {
				@include mixins.justify_width(start, start, row, true);
				align-items: center;
				color: variables.$textColorLightGray;

				&:before {
					display: block;
					flex-shrink: 0;
					content: "";
					width: 14px;
					height: 10px;
					margin: 0 2px 0 0;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/icon_item_view', 'png', 14px, 10px);
				}
			}
		}

		&.size_sm {
			.prod_name {
				@include mixins.font_size_xs;
				height: calc(#{variables.$fontSizeXsLineHeight} * 2);
				max-height: calc(#{variables.$fontSizeXsLineHeight} * 2);
			}

			.prod_author {
				@include mixins.font_size_xs;
			}
		}

		&.size_lg {
			.prod_name {
				@include mixins.font_size_def;
				height: calc(#{variables.$fontSizeDefLineHeight} * 2);
				max-height: calc(#{variables.$fontSizeDefLineHeight} * 2);
			}

			.prod_author {
				@include mixins.font_size_xs;
			}
		}

		&.size_xlg {
			.prod_name {
				@include mixins.font_size_md;
				height: calc(#{variables.$fontSizeMdLineHeight} * 2);
				max-height: calc(#{variables.$fontSizeMdLineHeight} * 2);
			}

			.prod_author {
				@include mixins.font_size_xs;
			}
		}
	}

	&:not(.horizontal) {
		.prod_info_box {
			margin-top: 16px;
		}
	}

	&.horizontal {
		overflow: hidden;
		width: 100%;
		@include mixins.justify_width(center);

		.prod_thumb_box {
			overflow: hidden;
			flex-shrink: 0;
			width: 142px;

			&.size_sm {
				width: 100px;
			}

			&.size_lg {
				width: 200px;
			}

			&.size_xlg {
				width: 276px;
			}
		}

		.prod_info_box {
			overflow: hidden;
			margin-left: 16px;

			.prod_name {

			}

			.prod_author {
				margin-top: 5px;
			}

			.prod_price {
				margin-top: 20px;
			}

			.prod_period {
				margin-top: 5px;
			}
		}
	}

	// 원형 상품이미지 타입
	&.type_round {
		box-shadow: none;

		.prod_thumb_box {
			border: none;

			.prod_link {
				.img_box {
					overflow: hidden;
					border-radius: 100%;
				}
			}
		}

		.prod_info_box {
			margin-top: 30px;
			text-align: center;
		}

		// 회색배경 타입 박스
		&.gray_box {
			padding: 40px 0 50px;
			border-radius: 10px;
			background-color: #fafafa;

			.prod_thumb_box {
				overflow: visible;

				.prod_link {
					width: 150px;
					height: 150px;
					margin: 0 auto;

					.img_box {
						overflow: hidden;
						width: 100%;
						height: 100%;
						border-radius: 100%;
					}
				}

				.btn_wish {
					top: -25px;
					right: 15px;
					bottom: auto;
				}
			}

			.prod_info_box {
				margin-top: 26px;

				.prod_tag {
					.btn_tag {
						padding: 5px 10px 6px;
						@include mixins.font_size_xxs();
						border-radius: 15px;
						background-color: #fff;
					}
				}
			}
		}
	}

	&.type_bookcast {
		.prod_thumb_box {
			border: none;
			border-radius: 10px;
		}

		&.horizontal {
			.prod_thumb_box {
				width: 160px;

				&.size_sm {
					width: 134px;
				}

				&.size_lg {
					width: 190px;
				}
			}

		}
	}
}

/* 상단 리스트 결과 정보 박스 */
.list_result_wrap {
	position: relative;
	@include mixins.justify_width(center);
	margin-bottom: 11px;

	.result_count {
		display: inline-block;
		@include mixins.font_size_def;
		font-weight: variables.$fontWeightBold;

	}

	.right_area {
		margin-left: auto;
		flex-shrink: 0;
		@include mixins.justify_width(center, start, row);

		.btn_filter {
			margin-right: 4px;

			&:last-child {
				margin-right: 0;
			}
		}
		.form_chk,
		.form_sel {
			margin-right: 20px;

			&:last-child {
				margin-right: 0;
			}
		}
	}
}

// 리스트 필터 박스
.list_filter_wrap {
	display:none;
	position:relative;
	padding:20px 180px 20px 20px;
	border-top: 1px solid variables.$colorGray03;

	.list_filter_option {
		margin:-20px 0 0;
		.list_filter_title {
			display:inline-block;
			margin:20px 20px 0 60px;
			@include mixins.font_size_xs();
			vertical-align:top;
			
			&:first-child {
				margin:20px 20px 0 0;
			}
		}
		.list_filter_option_bunddle {
			display:inline-block;
		}
		.form_chk {
			margin:20px 20px 0 0;
			&:last-child {
				margin-right:20px 0 0;
			}
		}
	}

	.util_filter {
		position: absolute;
		right: 60px;
		top: 15px;
		button {
			margin:0 14px 0 0;
			vertical-align:middle;

			&:last-child {
				margin:0;
			}
		}
	}

	.btn_filter_close {
		display: block;
		position: absolute;
		right: 20px;
		top: 20px;
		width: 20px;
		height: 20px;
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/btn_tooltip_close', 'png', 20px, 20px);
		box-sizing: border-box;
	}

	&.active {
		display:block;
		& + .prod_list_wrap {
			border-top:1px solid variables.$colorGray04;
		}
	}
}

// 상품 리스트 박스
.prod_list_wrap {
	padding: 30px 0 0;
	border-top: 1px solid variables.$colorGray03;

	.no_data {
		margin-top: -30px;
		border-bottom: 1px solid variables.$colorGray03;
	}

	.pagination {
		margin: 60px 0 0;
	}
}