@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

// SimpleBar
/* custom scroll */
.custom_scroll_wrap {
	//-ms-overflow-style: -ms-autohiding-scrollbar; // IE 스크롤 마우스 오버시에만 노출
	width: 100%;
	height: 100%;
	overflow-x: hidden;
	overflow-y: auto;
	box-sizing: border-box;

	.simplebar-track {
		width: 2px;
		background: rgba(variables.$textColorBlack, .06);
		border-radius: 2px;

		.simplebar-scrollbar {
			width: 2px;
			left: 0;
			border-radius: 2px;

			&:before {
				border-radius: 2px;
			}

			&.simplebar-visible {
				&:before {
					opacity: 1;
				}
			}
		}

		&.simplebar-vertical {
			.simplebar-scrollbar {
				&:before {
					left: 0;
					right: 0;
					top: 0;
					bottom: 0;
				}
			}
		}

		&.simplebar-horizontal {
			height: 2px;

			.simplebar-scrollbar {
				height: 2px;
				top: 0;

				&:before {
					left: 0;
					right: 0;
				}
			}
		}

	}

	&.horizontal {
		overflow: {
			x: auto;
			y: hidden;
		}
	}

	&.active {
		padding-right: 18px;
	}

	&.type_white {
		.simplebar-track {
			background: rgba(255, 255, 255, .06);

			.simplebar-scrollbar {
				&:before {
					background-color: #fff;
				}
			}
		}
	}
}

/* 팝업 - dialog */
body.dialog_open {
	position: static;
	overflow-y: hidden;
}

.dialog_wrapper {
	display: none;
	min-width: variables.$defaultMinWidth;
	z-index: 1000;
	align-items: center;

	.ui-widget-overlay {
		background: #000;
		opacity: 0.5;
		filter: alpha(opacity=50);
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
	}

	.ui-dialog {
		@include mixins.justify_width(start, start, column);
		position: relative;
		width: 560px;
		max-height: 750px;
		margin: 0 auto;
		padding: 0;
		border: none;
		color: #000;

		&:after {
			content: '';
			display: block;
			min-height: inherit;
			font-size: 0;
		}

		.ui-dialog-titlebar {
			display: none;
		}

		.btn_dialog_close {
			position: absolute;
			top: 28px;
			right: 30px;
			width: 24px;
			height: 24px;

			.ico_dialog_close {
				position: relative;
				z-index: 3;
				display: block;
				width: 24px;
				height: 24px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/btn_dialog_close', 'png', 24px, 24px);
			}
		}

		.dialog_wrap {
			&.ui-dialog-content {
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				display: -webkit-flex;
				-moz-box-orient: vertical;
				-ms-flex-direction: column;
				-webkit-flex-direction: column;
				flex-direction: column;
				overflow: hidden;
				width: 100% !important;
				max-height: 750px !important;
				padding: 0 30px 40px;
				background: #fff;
				border-radius: 16px;
				border: 0;
				box-sizing: border-box;

				&:after {
					content: '';
					display: block;
					min-height: inherit;
					font-size: 0;
				}

				.dialog_header {
					flex-shrink: 0;
					padding: 25px 30px 20px 0;
					text-align: left;
					background: #fff;
					border-bottom: 1px solid variables.$colorGray04;

					.dialog_title {
						@include mixins.font_size_md;
						font-weight: variables.$fontWeightMedium;
						@include mixins.ellipsis(false, false, true);
					}
				}

				.dialog_contents {
					flex: 1 1 auto;
					overflow: hidden;
					overflow-y: auto;
					position: relative;
					max-height: 610px;
					background: #fff;
					padding: 0;
					margin-top: 24px;
					box-sizing: border-box;
					-webkit-overflow-scrolling: touch;

					&:after {
						content: '';
						display: block;
						min-height: inherit;
						font-size: 0;
					}

					.custom_scroll_wrap {
						max-height: inherit;
					}

					.ico_info_wrap {
						text-align: center;

						.ico_img_box {
							margin-bottom: 20px;

							[class^=ico_] {
								display: inline-block;
								vertical-align: top;
							}
						}

						.ico_text_box {
							.ico_title {
								display: inline-block;
								color: variables.$textColorBlack;
								@include mixins.font_size_def;
								font-weight: variables.$fontWeightBold;

								& ~ .ico_desc {
									margin-top: 12px;
								}
							}

							.ico_desc {
								display: inline-block;
								color: variables.$textColorGray;
								@include mixins.font_size_xs;
								font-weight: variables.$fontWeightRegular;
							}
						}
					}

					.tbl_col_wrap {
						.tbl_col {
							tr {
								th {
									padding: 14px 10px;
									@include mixins.font_size_xxs;
								}

								td {
									@include mixins.font_size_xxs;
								}
							}
						}

						&:not(.type_th_gray) {
							.tbl_col {
								tbody {
									tr {
										:first-child {
											td {
												padding-top: 16px;
											}
										}

										&:last-child {
											td {
												padding-bottom: 20px;
											}
										}
									}

									td {
										padding: 10px 10px;
									}
								}
							}
						}
					}

					.thumbnail_round_box {
						padding: 16px;
						border: 1px solid variables.$colorGray04;
						border-radius: 10px;

						.thumbnail_product_item {
							.thumbnail_area {
								width: 64px;
							}

							.info_area {
								padding: 6px 0 6px 12px;
							}
						}

						+ .info_text_box {
							margin-top: 10px;
						}
					}

				}

				.dialog_footer {
					@include mixins.justify_width(center, center);
					flex-shrink: 0;
					margin-top: 34px;

					[class^=btn_] {
						flex: 0 auto;

						& ~ [class^=btn_] {
							margin-left: 6px;
						}
					}
				}

				&.has_btn {
					padding-bottom: 30px;

					.dialog_contents {
						max-height: 541px;
					}
				}
			}

			// no title
			&.no_title {
				padding: 0 30px 30px;

				.dialog_contents {
					max-height: 586px;
					margin-top: 56px;
					text-align: center;
					font-size: 0;
				}
			}

			&.no_title_line {
				.dialog_header {
					border-bottom: none;
					padding-bottom: 0;
				}

				.dialog_contents {
					max-height: 625px;
					margin-top: 30px;
				}

				&.has_btn {
					.dialog_contents {
						max-height: 556px;
					}
				}
			}
		}

		// large popup
		&.dialog_lg {
			width: 920px;
		}

		// small popup
		&.dialog_sm {
			width: 400px;

			.dialog_contents {
				.thumbnail_round_box {
					.thumbnail_product_item {
						.thumbnail_area {
							width: 46px;
						}
					}
				}
			}
		}

		//좌석배치도
		&.dialog_seating_chart {
			width: 1020px;

			img {
				width: 100%;
			}
		}

		// alert popup
		&.dialog_alert {
			width: 360px;

			.dialog_wrap {
				padding: 0 37px 30px;

				.dialog_contents {
					text-align: center;

					.alert_text {
						@include mixins.justify_width(center, center);
						width: 100%;
						min-height: 45px;
					}
				}
			}
		}

		// h_auto
		&.h_auto {
			height: 100vh !important;
		}
	}

	&.open {
		display: flex;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		overflow-y: hidden;
	}

	// no dim
	&.no_dim {
		overflow: visible;
		width: auto;
		min-width: 0;
		height: auto;
	}

}

/* sps */
.header_wrapper {
	&.sps {
		~ .container_wrapper {
			.sps {
				&.sps-blw {
					top: 80px;
				}
			}
		}
	}
}
.sps {
	&.sps_scroll_fixed {
		position: absolute;
		top: 0;
		width: 100%;
		background-color: #fff;
		z-index: 10;

		.sps_inner {
			width: variables.$defaultInnerWidth;
			margin: 0 auto;
			height: 100%;
		}

		~ .sps-bg {
			position: absolute;
			top: 0;
			left: 0;
		}

		&.sps-blw {
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			min-width: variables.$defaultMinWidth;
			width: 100%;
		}

		&.pos-right {
			right: 0;
			left: auto;
			margin-left: 0;
		}
	}
}

/* spinner */
.form_spinner_box {
	display: inline-block;
	vertical-align: top;

	&.form_spinner_box.disabled {
		.ui-spinner {
			.ui-spinner-button {
				.ui-icon {
					@include mixins.ratio_images('../images/ico_spinner_down_off', 'png', 12px, 12px);
				}

				&.ui-spinner-button.ui-spinner-up {
					.ui-icon {
						@include mixins.ratio_images('../images/ico_spinner_up_off', 'png', 12px, 12px);
					}
				}
			}
		}
	}

	.ui-spinner {
		position: relative;
		display: block;
		width: 102px;
		padding: 0 33px;
		background-color: #fff;
		box-sizing: border-box;
		font-size: 0;

		.form_spinner {
			width: 100%;
			height: 44px;
			margin: 0;
			padding: 1px 0 0 0;
			border-top: 1px solid variables.$colorGray02;
			border-bottom: 1px solid variables.$colorGray02;
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
			font-family: variables.$fontEng;
			outline: 0;
			text-align: center;
			box-sizing: border-box;
			vertical-align: top;
		}

		.ui-spinner-button {
			display: block;
			top: 0;
			width: 34px;
			height: 44px;
			background-color: #fff;
			border: 1px solid variables.$colorGray02;
			box-sizing: border-box;
			cursor: pointer;

			.ui-icon {
				width: 12px;
				height: 12px;
				@include mixins.ratio_images('../images/ico_spinner_down', 'png', 12px, 12px);
				text-align: center;
				vertical-align: top;
				margin: 0;
			}

			&.ui-spinner-button.ui-spinner-up {
				.ui-icon {
					@include mixins.ratio_images('../images/ico_spinner_up', 'png', 12px, 12px);
				}
			}

		}

		.ui-spinner-button.ui-spinner-up {
			border-left: none;
			border-radius: 0 6px 6px 0;
		}

		.ui-spinner-button.ui-spinner-down {
			left: 0;
			border-right: none;
			border-radius: 6px 0 0 6px;
		}
	}
}

// jquery ui selectmenu
.ui-selectmenu-menu {
	display: block;
	height: 0;
	overflow: hidden;
	box-sizing: border-box;
	background-color: #fff;
	margin-top: -1px;
	border-radius: 0 0 6px 6px;
	opacity: 0;
	@include mixins.transition(opacity .25s);

	&.ui-selectmenu-open {
		height: auto;
		opacity: 1;
		box-sizing: border-box;
	}

	.ui-menu {
		max-width: 100%;
		padding-bottom: 6px;
		box-sizing: border-box;
		border: 1px solid #000;
		border-top: none;
		border-radius: 0 0 6px 6px;
		overflow-y: auto;

		.ui-menu-item {
			.ui-menu-item-wrapper {
				padding: 10px 14px 9px;
				color: #000;
				@include mixins.font_size_xs;
				font-family: variables.$fontKor;
				box-sizing: border-box;
				@include mixins.transition(background-color 0.2s ease-out);

				&.ui-state-active {
					margin: 0;
					background-color: #f5f5f5;
				}

				&.prod_option {
					@include mixins.justify_width(center);

					.option_label_box {
						display: block;
						margin-right: 15px;
					}

					.option_price_box {
						margin-left: auto;
						flex-shrink: 0;
						display: block;

						.option_price {
							font-family: variables.$fontEng;
						}
					}
				}
			}

			&.ui-state-disabled {
				.ui-menu-item-wrapper {
					color: variables.$textColorLightGray;
				}
			}
		}
	}

	// select - sm type option
	&.type_sm {
		.ui-menu {
			.ui-menu-item {
				.ui-menu-item-wrapper {
					padding: 11px 14px;
					@include mixins.font_size_xxs;
				}
			}
		}
	}

	// select - arw type option
	&.type_arw {
		width: 72px;
		border-radius: 10px;

		.ui-menu {
			padding: 12px 12px;
			border: 0;

			.ui-menu-item {
				.ui-menu-item-wrapper {
					padding: 0;
					background: none;
					@include mixins.font_size_xxs;
					color: variables.$textColorLightGray;
					@include mixins.transition(color 0.2s ease-out);

					&.ui-state-active {
						margin: 0;
						color: #000;
					}
				}

				& ~ .ui-menu-item {
					margin-top: 10px;
				}
			}
		}

		&.ui-selectmenu-open {
			border: 1px solid #000;
		}
	}

	// select - gnb
	&.type_gnb {
		border-radius: 0 0 20px 20px;

		.ui-menu {
			border-radius: 0 0 20px 20px;
			padding-bottom: 10px;

			.ui-menu-item {
				.ui-menu-item-wrapper {
					padding: 3px 24px;
					color: variables.$textColorGray;

					&.ui-state-active {
						background-color: transparent;
					}
				}
			}
		}
	}
}

/* rating */
.rating-container {
	position: relative;
	display: inline-block;
	text-align: left;
	font-size: 0;
	vertical-align: top;
	margin-left: -2px;

	.clear-rating {
		display: none;
	}

	.rating-stars {
		.empty-stars {
			.star {
				margin: 0 2px;

				.glyphicon-star-empty:before {
					content: '';
					display: inline-block;
					width: 18px;
					height: 18px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_none', 'png', 18px, 18px);
				}
			}
		}

		.filled-stars {
			.star {
				margin: 0 2px;

				.glyphicon-star:before {
					content: '';
					display: inline-block;
					width: 18px;
					height: 18px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover', 'png', 18px, 18px);
				}
			}
		}

		.form_rating {
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
			font-family: variables.$fontEng;
			outline: 0;
		}
	}

	.caption {
		display: none;
	}

	&.rating-sm {
		margin-left: -1px;

		.empty-stars {
			.star {
				margin: 0 0 0 1px;

				.glyphicon-star-empty:before {
					content: '';
					display: inline-block;
					width: 14px;
					height: 14px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_sm_none', 'png', 14px, 14px);
				}
			}
		}

		.filled-stars {
			.star {
				margin: 0 0 0 1px;

				.glyphicon-star:before {
					content: '';
					display: inline-block;
					width: 14px;
					height: 14px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_sm', 'png', 14px, 14px);
				}
			}
		}
	}

	&.has_caption {
		.caption {

			display: inline-block;
			margin: -1px 0 0 8px;
			vertical-align: top;
			font-size: 0;

			.label {
				padding: 0;
				border-radius: 0;
				font-size: 0;

				.val {
					@include mixins.font_size_md;
					color: variables.$textColorGreen;
					font-weight: variables.$fontWeightBold;
					line-height: 1;
				}

				.total {
					padding-top: 5px;
					@include mixins.font_size_xs;
					color: variables.$textColorLightGray;
					line-height: 1;

					&:before {
						content: '/';
					}
				}
			}
		}
	}
}

.rating-disabled {
	.rating-input {
		cursor: default;
	}

	.rating-stars {
		cursor: default;
	}
}

/* datepicker */
.datepicker_box {
	position: relative;
	width: 100%;

	.form_datepicker {
		display: inline-block;
		width: 100%;
		height: 44px;
		padding: 0 14px;
		color: variables.$textColorBlack;
		font-size: variables.$fontSizeXs;
		line-height: 42px;
		letter-spacing: variables.$letterSpacing;
		font-family: variables.$fontKor;
		background-color: #fff;
		border: 1px solid variables.$colorGray03;
		border-radius: 6px;
		box-sizing: border-box;
		@include mixins.transition(border-color .2s ease-out);
		outline: none;

		&:active, &:focus {
			border-color: #000;
		}

		&[disabled], &[aria-disabled], &[readonly] {
			color: variables.$textColorLightGray;
			background-color: variables.$colorGray06;
			pointer-events: none;
			cursor: default;
		}

		@include mixins.set_placeholder_color(variables.$textColorLightGray);
	}

	.ui-datepicker-trigger {
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		width: 36px;
		height: 36px;
		background: #ff1493; // 임시
	}
}

/* swiper */
.swiper-button-next, .swiper-container-rtl .swiper-button-prev {
	background-color: transparent;
}
.swiper-button-next,
.swiper-button-prev {
	opacity: 1;
	width: 44px;
	height: 44px;
	margin-top: -22px;
	background: {
		position: center;
		repeat: no-repeat;
		color: rgba(variables.$textColorBlack, .3);
	}
	@include mixins.ratio_images('../images/btn_swiper_arr_white', 'png', 44px, 44px);
	@include mixins.transition(opacity .23s);
	border-radius: 100%;
	z-index: 2;
	box-sizing: border-box;

	&.swiper-button-disabled {
		opacity: .3;
	}

	&.type_white {
		border: 1px solid variables.$colorGray02;
		background-color: #fff;
		@include mixins.ratio_images('../images/btn_swiper_arr', 'png', 44px, 44px);
	}
}
.swiper-button-prev {
	@include mixins.transform(rotate(180deg));
}

.swiper-pagination {
	&.swiper-pagination-fraction {
		left: 50%;
		bottom: 20px;
		transform: translateX(-50%);
		width: auto;
		min-width: 60px;
		height: 29px;
		padding: 0 14px;
		line-height: 29px;
		border-radius: 20px;
		background-color: rgba(0, 0, 0, .6);
		box-sizing: border-box;
		font-size: 0;

		.swiper-pagination-current,
		.swiper-pagination-total {
			display: inline-block;
			color: variables.$colorGray02;
			font-size: variables.$fontSizeXxs;
			letter-spacing: variables.$letterSpacing;
			line-height: 29px;
			font-family: variables.$fontEng;
			vertical-align: top;
		}

		.swiper-pagination-current {
			color: #fff;
			font-weight: variables.$fontWeightBold;
		}

		.swiper-pagination-total {
			&:before {
				content: '-';
				display: inline-block;
				margin: 0 4px 0 3px;
				color: variables.$colorGray02;
			}
		}
	}
}

.swiper-container-horizontal {
	& > .swiper-scrollbar {
		position: absolute;
		left: 0;
		bottom: 0;
		z-index: 50;
		width: 100%;
		height: 2px;
		background: rgba(0, 0, 0, .06);
		border-radius: 2px;
		overflow: hidden;

		.swiper-scrollbar-drag {
			background: #000;
			border-radius: 2px;
		}
	}
}

.swiper_control_box {
	text-align: center;
	font-size: 0;

	.swiper-button-prev,
	.swiper-button-next {
		display: inline-block;
		position: static;
		width: 16px;
		height: 16px;
		margin-top: 0;
		background: {
			position: right center;
			repeat: no-repeat;
			color: transparent;
		}
		@include mixins.ratio_images('../images/btn_swiper_list', 'png', 6px, 10px);
		vertical-align: top;

		&.swiper-button-disabled {
			opacity: .3;
		}
	}

	.swiper-pagination {
		display: inline-block;
		position: static;
		min-width: auto;
		-webkit-transform: translateX(0);
		transform: translateX(0);
		vertical-align: top;

		&.swiper-pagination-fraction {
			bottom: 0;
			height: 16px;
			padding: 0 2px;
			border-radius: 0;
			background: none;

			.swiper-pagination-current,
			.swiper-pagination-total {
				color: variables.$textColorLightGray;
				font-size: variables.$fontSizeMin;
				letter-spacing: variables.$letterSpacing;
				line-height: 16px;
			}

			.swiper-pagination-current {
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightMedium;
			}

			.swiper-pagination-total {
				&:before {
					margin: 0 3px;
					color: variables.$textColorLightGray;
				}
			}
		}
	}
}

.swiper_auto_control_area {
	display: inline-block;
	position: absolute;
	right: 14px;
	z-index: 2;
	bottom: 14px;
	height: 30px;
	font-size: 0;

	.swiper_control_box {
		display: inline-block;
		min-width: 84px;
		height: 30px;
		padding: 7px 12px;
		background: rgba(0, 0, 0, .4);
		border-radius: 30px;
		box-sizing: border-box;

		.swiper-pagination {
			padding: 0;

			&.swiper-pagination-fraction {
				.swiper-pagination-current {
					color: #fff;
				}

				.swiper-pagination-total {
					color: variables.$colorGray04;

					&:before {
						color: variables.$colorGray04;
					}
				}
			}
		}

		.swiper-button-prev,
		.swiper-button-next {
			display: inline-block;
			position: static;
			width: 12px;
			height: 16px;
			margin-top: 0;
			background: {
				position: right center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/btn_swiper_list_wh', 'png', 6px, 10px);
			vertical-align: top;

			&.swiper-button-disabled {
				opacity: 1;
				background: {
					position: right center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/btn_swiper_list_wh_disabled', 'png', 6px, 10px);
			}
		}
	}

	.btn_auto_control {
		display: inline-block;
		width: 30px;
		height: 30px;
		margin-left: 4px;
		line-height: 30px;
		background: rgba(0, 0, 0, .4);
		border-radius: 30px;
		box-sizing: border-box;

		.ico_swiper_control {
			display: inline-block;
			width: 7px;
			height: 10px;
			margin-top: 10px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_swiper_pause', 'png', 6px, 8px);
			vertical-align: top;
		}

		&.pause {
			.ico_swiper_control {
				background: {
					position: 1px center;
				}
				@include mixins.ratio_images('../images/ico_swiper_play', 'png', 7px, 10px);
			}
		}
	}
}

/* jQuery UI - Slider */
.slider_item {
	height: 4px;
	background: #eaeaea;
	margin: 7px 0;
	border-radius: 2px;

	.ui-slider-range {
		background: #767675;
		border-radius: 2px;
	}

	.ui-slider-handle {
		width: 16px;
		height: 16px;
		margin-left: -9px;
		background: #fff;
		border: 1px solid #999;
		border-radius: 9px;
		top: -6px;
	}
}

.slider_wrap {
	@include mixins.justify_width(center);

	.slider_item {
		flex: 1;
		margin: {
			left: 14px;
			right: 14px;
		}
	}
}