									}

									.member_address {
										display: block;
										margin-top: 6px;
										color: variables.$textColorDarkGray;
										@include mixins.font_size_sm;
									}
								}

								.btn_wrap {
									margin: 0 8px 0 20px;
								}
							}

							.bul_item_asterisk {
								margin-top: 11px;
							}
						}

						// 배송지 찾기 영역
						.address_contents_inner {
							@include mixins.justify_width(start, start, column);
							width: 100%;
							height: 100%;

							// 우편번호 찾기 팝업 검색전
							.address_tip {
								width: 500px;
								padding: 16px;
								margin-top: 20px;
								background-color: variables.$colorGray06;
								border-radius: 10px;
								box-sizing: border-box;
								letter-spacing: variables.$letterSpacing;

								.tip {
									font-size: variables.$fontSizeXs;
									color: variables.$textColorBlack;
									line-height: 19px;
									margin: 0 0 10px 0;
								}

								.address_tip_item {
									position: relative;
									padding-left: 9px;
									margin-bottom: 6px;
									font-size: variables.$fontSizeXxs;
									color: variables.$textColorGray;
									line-height: 19px;

									&:before {
										content: '';
										position: absolute;
										top: 50%;
										transform: translateY(-50%);
										left: 0;
										width: 3px;
										height: 3px;
										border-radius: 50%;
										background-color: variables.$colorGray01;
									}
								}
							}

							// 검색결과 있음
							.address_search_total {
								width: 100%;
								margin-top: 20px;
								border-bottom: 1px solid variables.$textColorBlack;
								padding-bottom: 13px;
								margin-bottom: 14px;

								.address_search_total_text {
									font-size: variables.$fontSizeSm;
									color: variables.$textColorBlack;
									font-weight: variables.$fontWeightBold;

									.search_value {
										color: variables.$textColorPurple;
									}

									& + .info_text {
										margin-top: 7px;

										& + .address_sel_wrap {
											margin-top: 11px;
										}
									}
								}

								.address_sel_wrap {
									width: 100%;
									height: 44px;
									display: block;
									font-size: 0;

									.form_sel {
										width: calc((100% - 20px) / 2);

										.ui-selectmenu-button {
											width: 100%;
										}

										& + .form_sel {
											margin-left: 20px;
										}
									}
								}
							}

							.address_success_result {
								width: 100%;

								.address_success_wrap {
									.address_success_list {
										.address_success_item {
											@include mixins.justify_width(center, between);
											font-size: variables.$fontSizeXs;
											flex-wrap: wrap;
											margin-bottom: 14px;
											padding-bottom: 14px;
											border-bottom: 1px solid variables.$colorGray04;

											&.address_success_item:last-child {
												margin-bottom: 0;
											}

											.success_item_text {
												width: 85%;

												.road_name_wrap,
												.land_number_wrap {
													@include mixins.justify_width;

													.badge_md {
														display: inline-block;
														font-size: variables.$fontSizeMin;
														width: 44px;
														height: 22px;
														line-height: 22px;
														text-align: center;
														box-sizing: border-box;
														padding: 0;
													}

													.btn_road_name,
													.btn_land_number {
														width: 85%;
														margin-left: 6px;
														padding-top: 2px;
														text-align: left;

														&:hover {
															text-decoration: underline;
														}
													}

												}

												.road_name_wrap {
													margin-bottom: 8px;
												}

												// .land_number_wrap{
												// 	color: variables.$textColorGray;
												// 	.ico_land_number{
												// 		border: 1px solid variables.$colorGray03;
												// 	}
												// }
											}

											.uccess_item_post {
												width: 15%;
												text-align: right;
											}

											.success_detail {
												@include mixins.justify_width;
												width: 100%;
												margin-top: 12px;
												display: none;

												input[type=text] {
													width: 408px;
													margin-right: 6px;
												}

												&.active {
													@include mixins.justify_width;
												}
											}
										}
									}

								}
							}

							.pagination {
								width: 100%;

								~ .bul_item_dot {
									margin-top: 30px;
								}
							}

							.bul_item_dot {
								width: 100%;
								margin-top: auto;
							}

							.address_search_result_box {
								width: 100%;

								.bul_item_dot {
									margin-top: 30px;
								}
							}

							// 우편번호 찾기 팝업(검색결과 없음)
							.search_fail {
								width: 100%;
								padding: 16px;
								background-color: variables.$colorGray06;
								color: variables.$textColorGray;
								font-size: variables.$fontSizeXxs;
								box-sizing: border-box;
								border-radius: 10px;
								margin-top: 20px;

								.text_search_fail {
									color: variables.$textColorBlack;
									font-size: variables.$fontSizeXs;
									font-weight: variables.$fontWeightBold;
									margin-bottom: 10px;

									.fail_search_value {
										color: variables.$textColorPurple;
									}
								}
							}

							& + .bul_item_dot {
								margin-top: 30px;
							}
						}
					}

					// 바로배송 변경지 팝업 검색전
					&.has_btn {
						.dialog_contents {
							max-height: 548px;
							margin-bottom: 16px;
						}

						.dialog_footer {
							margin-top: 0;
							padding-top: 18px;
							border-top: 1px solid variables.$colorGray04;

							.login_info_text {
								flex: 1;
								@include mixins.font_size_xs;
								color: variables.$textColorBlack;
							}
						}
					}
				}

			}

			// 매장별 재고 및 위치 팝업
			&.dialog_stock {
				.dialog_contents {
					.tbl_col_wrap {
						.tbl_col {
							tbody {
								td {
									.btn_text_link {
										@include mixins.font_size_xxs;
										color: variables.$textColorBlack;
									}

								}
							}
						}
					}
				}
			}

			&.dialog_author {
				.number_text_list {
					.number_text_item {
						@include mixins.font_size_sm;

						.number_text {
							font-weight: variables.$fontWeightMedium;
							@include mixins.ellipsis();

							& + .round_gray_box {
								margin-top: 13px;
							}
						}

						.round_gray_box {
							.title_wrap {
								.title_heading {
									font-weight: variables.$fontWeightMedium;
								}
							}
						}

						& + .number_text_item {
							margin-top: 20px;
						}
					}
				}

				//저자 모두보기
				.btn_text_link {
					position: relative;
					color: variables.$textColorGray;
					padding-right: 3px;

					&:after {
						position: absolute;
						right: 0;
						bottom: 0;
						content: ',';
						display: inline-block;
					}

					&:last-child {
						&:after {
							display: none;
						}
					}
				}
			}

			//혜택안내 팝업
			&.dialog_add_points {
				.dialog_contents {
					.info_text {
						color: variables.$textColorDarkGray;
					}
				}
			}

			&.dialog_review {
				// 상품리뷰 팝업 내
				.all_review_wrap {
					.all_review_list {
						.all_review_item {
							margin-top: 0;
							padding: 0;
							border-bottom: none;

							.review_middle {
								.review_middle_box {
									.info_text {
										display: block;
										width: 100%;
										@include mixins.font_size_xs;
										color: variables.$textColorBlack;
									}

									.review_swiper {
										position: relative;
										width: 100%;
										padding: 0 22px;
										margin-top: 16px;
										box-sizing: border-box;

										.swiper-container {
											width: 100%;

											.swiper-wrapper {
												.swiper-slide {
													width: 300px;
													height: 300px;
													margin-right: 20px;
													border-radius: 10px;
													overflow: hidden;

													.main_pop_link {
														display: block;
														width: 100%;
														height: 100%;
													}

													img {
														width: 100%;
													}
												}
											}
										}

										.swiper-button-next {
											right: 0;
										}

										.swiper-button-prev {
											left: 0;
										}
									}
								}
							}

							.review_reply {
								display: block;
								padding-top: 12px;
								margin-top: 20px;
								border-top: 1px solid variables.$colorGray04;

								.byte_check_wrap {
									display: none;

									&.active {
										display: block;
									}

									.byte_chack_footer {
										.byte_check {
											margin-top: 7px;

											& + .btn_wrap {
												margin-left: 6px;
											}
										}
									}
								}

								.reply_list {
									.reply_list_item {
										position: relative;
										padding: 20px 20px 24px 37px;
										box-sizing: border-box;
										background-color: variables.$colorGray06;
										margin-top: 8px;
										border-radius: 10px;

										.review_user_info {
											margin-bottom: 9px;

											&:before {
												content: '';
												display: inline-block;
												width: 8px;
												height: 9px;
												@include mixins.ratio_images('../images/arw_reply', 'png', 8px, 9px);
												position: absolute;
												top: 24px;
												left: 20px;
											}
										}

										.info_text {
											color: variables.$textColorBlack;
										}
									}
								}
							}
						}
					}
				}
			}

			.dialog_contents {
				.toggle_textarea_list {
					.chk_col_item {
						.form_rdo {
							& ~ .byte_check_wrap {
								margin-top: 10px;
							}
						}
					}
				}

				.info_text {
					& + .bul_list {
						margin-top: 8px;
					}

					& + .title_wrap {
						margin-top: 20px;
					}
				}

				.bul_list {
					& + .btn_more_view {
						margin-top: 11px;
					}

					& + .tbl_col_wrap {
						margin-top: 12px;
					}

					& + .info_text_box {
						margin-top: 12px;
					}

					& + .bul_list {
						margin-top: 10px;
					}
				}

				.round_gray_box {
					& + .bul_item_asterisk {
						margin-top: 10px;
					}

					& + .bul_list {
						margin-top: 13px;
					}

					& + .title_wrap {
						margin-top: 20px;
					}
				}

				.info_text_box {
					& ~ .form_wrap {
						margin-top: 20px;
					}

					.btn_more_view {
						margin-top: 6px;
					}

					&.size_sm {
						.btn_more_view_sm {
							margin-top: 8px;
						}
					}
				}

				.tbl_col_wrap {
					& + .bul_item_asterisk {
						margin-top: 10px;
					}

					& + .title_wrap {
						margin-top: 20px;
					}

					& + .bul_list {
						margin-top: 20px;
					}
				}

				.bul_item_asterisk {
					& + .title_wrap {
						margin-top: 20px;
					}
				}

				// 타이틀 전제보기 팝업 내
				.prod_detail_title_wrap {
					// 타이틀
					.prod_title_area {
						display: block;
						position: relative;

						.prod_title_box {
							display: block;
							position: relative;
							text-align: left;
							font-size: 0;

							.prod_label {
								display: inline;
								position: relative;
								padding-right: 7px;
								color: variables.$textColorPurple;
								@include mixins.font_size_def;
								font-weight: variables.$fontWeightRegular;

								& ~ .prod_label {
									padding-left: 2px;

									&:after {
										content: '·';
										display: block;
										position: absolute;
										left: -4px;
										top: 50%;
										font-size: variables.$fontSizeDef;
										line-height: 1;
										font-weight: variables.$fontWeightRegular;
										letter-spacing: variables.$letterSpacing;
										@include mixins.transform(translateY(-50%));
									}
								}
							}

							.gap {
								visibility: hidden;
							}

							.prod_alias {
								display: inline;
								position: relative;
								padding-right: 7px;
								color: variables.$textColorBlack;
								@include mixins.font_size_def;
								font-weight: variables.$fontWeightMedium;
							}

							.prod_title {
								display: inline;
								color: variables.$textColorBlack;
								@include mixins.font_size_def;
								font-weight: variables.$fontWeightBold;
							}
						}
					}

					// 부제명 1,2, MD 부제
					.prod_desc_area {
						display: block;
						margin-top: 11px;

						.prod_desc_box {
							display: block;
							position: relative;
							text-align: left;
							font-size: 0;

							.prod_desc {
								display: inline;
								color: variables.$textColorGray;
								@include mixins.font_size_xs;
								font-weight: variables.$fontWeightMedium;

								&.normal {
									color: variables.$textColorLightGray;
									font-weight: variables.$fontWeightRegular;
								}
							}

							.gap {
								display: inline-block;
								width: 1px;
								height: 10px;
								margin: 5px 10px 0;
								background-color: variables.$colorGray02;
								font-size: 0;
								vertical-align: top;
							}

							& ~ .prod_desc_box {
								margin-top: 10px;
							}
						}
					}
				}
			}
		}
	}
}

// sns 공유하기
.sns_share_wrap {
	position: relative;
	display: inline-block;
	width: 42px;
	height: 42px;
	vertical-align: top;

	// 공유하기 버튼
	.btn_share {
		width: 42px;
		height: 42px;
		line-height: 40px;
		border: 1px solid variables.$colorGray02;
		text-align: center;
		border-radius: 50%;
		box-sizing: border-box;

		.ico_share {
			display: inline-block;
			width: 14px;
			height: 14px;
			margin-top: 13px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_share', 'png', 14px, 14px);
			vertical-align: top;
		}
	}

	.sns_share_box {
		position: absolute;
		top: 0;
		right: 0;
		width: 231px;
		border: 1px solid variables.$colorGray02;
		border-radius: 31px;
		padding: 7px 0 7px 10px;
		display: none;
		background-color: #fff;
	}

	&.size_sm {
		width: 36px;
		height: 36px;

		.btn_share {
			width: 36px;
			height: 36px;
			line-height: 34px;

			.ico_share {
				margin-top: 10px;
			}
		}

		.sns_share_box {
			width: 205px;
			padding: 4px 0 4px 5px;

			.btn_sns_share {
				~ .btn_sns_share {
					margin-left: 6px;
				}
			}

			.btn_sns_share_close {
				margin-left: 11px;
			}
		}
	}

	&.active {
		.sns_share_box {
			@include mixins.justify_width(center);
		}

		.btn_share {
			display: none;
		}
	}
}

.sns_share_box {
	@include mixins.justify_width(center);
	box-sizing: border-box;

	.btn_sns_share {
		width: 28px;
		height: 28px;
		background: {
			position: center;
			repeat: no-repeat;
		}

		&.kakao {
			@include mixins.ratio_images('../images/btn_kakao_share', 'png', 28px, 28px);
		}

		&.band {
			@include mixins.ratio_images('../images/btn_band_share', 'png', 28px, 28px);
		}

		&.facebook {
			@include mixins.ratio_images('../images/btn_facebook_share', 'png', 28px, 28px);
		}

		&.twitter {
			@include mixins.ratio_images('../images/btn_twitter_share', 'png', 28px, 28px);
		}

		&.url_copy {
			@include mixins.ratio_images('../images/btn_copy_share', 'png', 28px, 28px);
		}

		~ .btn_sns_share {
			margin-left: 10px;
		}
	}

	.btn_sns_share_close {
		width: 13px;
		height: 13px;
		margin-left: 14px;
		@include mixins.ratio_images('../images/btn_share_close', 'png', 13px, 13px);
	}
}