@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* tootip */
.tooltip_wrap {
	position: relative;
	display: inline-block;
	height: 22px;
	vertical-align: top;
	box-sizing: border-box;

	.btn_tooltip {
		display: inline-block;
		position: relative;
		width: 22px;
		height: 22px;

		// 아이콘
		[class^="ico_"] {
			display: inline-block;
			width: 22px;
			height: 22px;
			vertical-align: top;
			//@include mixins.transition(background 0.2s ease-out);
			background: {
				position: center;
				repeat: no-repeat;
			}
		}

		.ico_info {
			@include mixins.ratio_images('../images/btn_tooltip', 'png', 22px, 22px);
		}

		.ico_arw {
			@include mixins.ratio_images('../images/arw_tooltip', 'png', 22px, 22px);
		}
	}

	&.active {
		.btn_tooltip {
			.ico_info {
				@include mixins.ratio_images('../images/btn_tooltip_active', 'png', 22px, 22px);
			}

			.ico_arw {
				@include mixins.ratio_images('../images/arw_tooltip_active', 'png', 22px, 22px);
			}
		}

		.tooltip_inner {
			opacity: 1;
			@include mixins.transform(scaleY(1));
		}
	}

	.tooltip_inner {
		display: block;
		position: absolute;
		left: 0;
		top: 22px;
		z-index: 10;
		overflow: hidden;
		width: 330px;
		padding: 19px 19px;
		margin-top: 6px;
		background-color: #fff;
		border: 1px solid #000;
		border-radius: 10px;
		text-align: left;
		box-sizing: border-box;
		opacity: 0;
		@include mixins.transform(scaleY(0));
		@include mixins.transition(opacity 0.3s ease-out);

		.tooltip_header {
			padding-bottom: 16px;
			box-sizing: border-box;
			text-align: center;

			.tooltip_head_title {
				@include mixins.font_size_def;
				font-weight: variables.$fontWeightMedium;
			}
		}

		.tooltip_contents {
			box-sizing: border-box;

			.tooltip_sub_title {
				color: variables.$textColorBlack;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightBold;

				& ~ .tooltip_desc {
					margin-top: 7px;
				}

				& ~ .bul_item_dot,
				& ~ .bul_list {
					margin-top: 9px;
				}
			}

			.tooltip_desc {
				color: variables.$textColorDarkGray;
				@include mixins.font_size_xxs;
				font-weight: variables.$fontWeightRegular;

				& ~ .tooltip_sub_title {
					margin-top: 16px;
				}

				& ~ .tooltip_desc {
					margin-top: 6px;
				}

				& ~ .bul_item_asterisk.font_size_xxs {
					margin-top: 6px;
				}

				& ~ .info_text_box {
					margin-top: 18px;
				}

				.btn_text_link {
					display: inline;
					color: inherit;
					font-size: inherit;
					line-height: inherit;

					.text {
						display: inline;
					}
				}
			}

			.tooltip_info_list {
				display: block;

				.tooltip_info_item {
					display: block;
					color: variables.$textColorBlack;
					box-sizing: border-box;

					& ~ .tooltip_info_item {
						margin-top: 13px;
						padding-top: 15px;
						border-top: 1px solid variables.$colorGray04;
					}
				}
			}

			.bul_list {
				& ~ .info_text_box {
					margin-top: 10px;
				}
			}
		}
	}

	.btn_tooltip_close {
		display: block;
		position: absolute;
		right: 20px;
		top: 20px;
		width: 20px;
		height: 20px;
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/btn_tooltip_close', 'png', 20px, 20px);
		box-sizing: border-box;
	}

	&.type_sm {
		height: 16px;

		.btn_tooltip {
			width: 16px;
			height: 16px;

			[class^="ico_"] {
				width: 16px;
				height: 16px;

				background: {
					position: center;
					repeat: no-repeat;
				}
			}

			.ico_info {
				@include mixins.ratio_images('../images/btn_tooltip_sm', 'png', 16px, 16px);
			}

			.ico_question {
				@include mixins.ratio_images('../images/btn_tooltop_question_sm', 'png', 16px, 16px);
			}
		}

		&.active {
			.btn_tooltip {
				.ico_info {
					@include mixins.ratio_images('../images/btn_tooltip_sm_active', 'png', 16px, 16px);
				}
			}
		}

		.tooltip_inner {
			top: 16px;
		}

		&.bottom {
			.tooltip_inner {
				bottom: 16px;
			}
		}
	}

	&.type_info {
		.tooltip_inner {
			padding: 16px 17px 18px;
			margin-top: 10px;
			border-radius: 6px;
			box-shadow: 0 5px 10px rgba(0, 0, 0, 0.12);
		}
	}

	&.right {
		.tooltip_inner {
			left: auto;
			right: 0;
		}
	}

	&.bottom {
		.tooltip_inner {
			margin: {
				top: 0;
				bottom: 6px;
			}
			top: auto;
			bottom: 22px;
		}
	}

	&.hide {
		.tooltip_inner {
			@include mixins.transform(scaleY(0));
			@include mixins.transition(opacity 0.3s ease-out, transform 0s .3s);
		}
	}
}