@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

$badgeProps: (
		'border-radius': 4px,
		'border-radius-lg': 6px,
		'height-md': 22px,
		'height-sm': 20px,
		'padding': 1px 5px,
		'padding-sm' : 0 7px,
);

/* 뱃지 */
[class^=badge_] {
	display: inline-block;
	box-sizing: border-box;
	text-align: center;
	vertical-align: top;

	// 아이콘
	[class^=ico_] {
		display: inline-block;
		position: relative;
		vertical-align: top;
	}

	.text {
		display: inline-block;
		position: relative;
		vertical-align: top;
	}
}

/* 뱃지 - 사이즈 */
.badge_md {
	min-height: map.get($badgeProps, 'height-md');
	padding: map.get($badgeProps, 'padding');
	border-radius: map.get($badgeProps, 'border-radius');
	@include mixins.font_size_min;
}
.badge_sm {
	min-height: map.get($badgeProps, 'height-sm');
	padding: map.get($badgeProps, 'padding-sm');
	border-radius: map.get($badgeProps, 'border-radius');
	@include mixins.font_size_min;

	/* 뱃지 - pill */
	&.badge_pill {
		border-radius: 20px;
	}
}

/* 뱃지 - 색상 */
.badge_line_primary {
	color: variables.$textColorPurple;
	border: 1px solid variables.$textColorPurple;
	background: #fff;
}
.badge_line_gray {
	color: variables.$textColorLightGray;
	background: #fff;
	border: 1px solid variables.$colorGray02;
}

/* 뱃지 - 리스트 */
.badge_list_box {
	margin-top: -4px;
	font-size: 0;
	text-align: left;

	[class^=badge_] {
		margin-right: 4px;
		margin-top: 4px;
	}
}

/* 뱃지 - 채널별 색상 */
.badge_kyobo {
	color: variables.$textColorPurple;
	border: 1px solid variables.$textColorPurple;
	background: #fff;
}
.badge_ebook {
	color: variables.$textColorPurple;
	border: 1px solid variables.$textColorPurple;
	background: #fff;
}
