@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

// 이미지 가로형/세로형 체크 컨텐츠 공통
.portrait_img_box {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	overflow: hidden;

	img {
		position: absolute;
		display: block;
		margin: 0 auto;
		top: 50%;
		left: 50%;
		@include mixins.transform(translate(-50%, -50%));
	}

	&.landscape, &.even {
		img {
			max-width: 100%;
		}
	}

	&.portrait {
		img {
			max-height: 100%;
		}
	}
}

// 블러이미지 컨텐츠 공통
.blur_img_wrap {
	position: relative;

	.blur_img_box {
		position: relative;
		overflow: hidden;

		img {
			position: absolute;
			top: 50%;
			left: 50%;
			@include mixins.transform(translate(-50%, -50%));
		}
	}

	&.landscape, &.even {
		.blur_img_box {
			height: 100%;

			img {
				height: 100%;
			}
		}

		.portrait_img_box {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			img {
				max-width: 100%;
			}
		}
	}

	&.portrait {
		.blur_img_box {
			display: none;
		}

		.portrait_img_box {
			img {
				position: static;
				width: 100%;
				@include mixins.transform(none);
			}
		}
	}
}

// contents gray box
.round_gray_box {
	display: block;
	padding: 36px 30px 40px;
	border-radius: 10px;
	background-color: variables.$colorGray06;
	box-sizing: border-box;

	.bottom_btn_area {
		margin: 30px 0 -10px;
		text-align: center;
	}

	.explan_text_list {
		font-size: 0;

		.explan_title {
			display: inline-block;
			width: 122px;
			padding-right: 12px;
			@include mixins.font_size_xxs;
			font-weight: variables.$fontWeightMedium;
			vertical-align: top;
			box-sizing: border-box;

		}

		.explan_desc {
			display: inline-block;
			width: calc(100% - 122px);
			@include mixins.font_size_xxs;
			color: variables.$textColorLightGray;
			vertical-align: top;

			~ .explan_title, ~ .explan_desc {
				border-top: 1px solid variables.$colorGray04;
				margin-top: 18px;
				padding-top: 18px;
			}
		}
	}

	+ .round_gray_box {
		margin-top: 20px;
	}

	&.type_sub {
		padding: 24px;
	}

	&.type_sub_sm {
		padding: 20px;
	}
}

/* auto overflow 공통 */
.auto_overflow_wrap {
	.auto_overflow_contents {
		overflow: hidden;
	}

	.auto_overflow_footer {
		display: none;
	}

	&.overflow {
		.auto_overflow_footer {
			display: block;
			margin-top: 12px;

			&.type_cont {
				margin-top: 30px;
				text-align: center;
			}
		}
	}

	&.active {
		> .auto_overflow_contents {
			max-height: none !important;
		}

		&.type_tbl_col {
			.auto_overflow_contents {
				&:after {
					content: none;
				}
			}
		}
	}

	&.type_tbl_col {
		.auto_overflow_contents {
			position: relative;
			border-bottom: 1px solid variables.$colorGray04;

			&:after {
				content: '';
				position: absolute;
				display: block;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 10px;
				background-color: #fff;
			}
		}

		.tbl_col_wrap {
			border-bottom: none;
		}
	}

	&.type_text {
		&.overflow {
			.auto_overflow_contents {
				position: relative;

				&:after {
					content: '';
					width: 100%;
					height: 15px;
					background: linear-gradient(180deg, transparent, #fff 15px);
					position: absolute;
					bottom: 0;
				}
			}

			&.active {
				.auto_overflow_contents {
					&:after {
						content: none;
					}
				}
			}
		}
	}
}

/* 상품/리뷰 컨텐츠 공통 사용 */
// " 좋았어요 텍스트
.review_quotes_text {
	display: inline-block;
	position: relative;
	padding-left: 18px;
	color: variables.$textColorPurple;
	@include mixins.font_size_xs;
	font-weight: variables.$fontWeightMedium;
	vertical-align: top;

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 3px;
		width: 13px;
		height: 13px;
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/ico_quotes', 'png', 13px, 13px);
	}

	&.font_size_xxs {
		@include mixins.font_size_xxs;

		&:before {
			top: 2px;
		}
	}
}

// 클로버 아이콘 + 점수 텍스트
.review_klover_text {
	display: inline-block;
	position: relative;
	padding-left: 19px;
	color: variables.$textColorBlack;
	@include mixins.font_size_xs;
	font-weight: variables.$fontWeightBold;
	vertical-align: top;

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 4px;
		width: 14px;
		height: 14px;
		background: {
			repeat: no-repeat;
			position: center;
		}
		@include mixins.ratio_images('../images/ico_klover_sm', 'png', 14px, 14px);
	}

	&.font_size_xxs {
		@include mixins.font_size_xxs;

		&:before {
			top: 3px;
		}
	}
}

// 클로버 아이콘 + 점수 텍스트 / 리뷰 갯수 텍스트
.review_klover_box {
	display: inline-block;
	font-size: 0;
	vertical-align: top;

	.review_desc {
		display: inline-block;
		margin: 1px 0 0 4px;
		color: variables.$textColorLightGray;
		@include mixins.font_size_min;
		vertical-align: top;
	}
}

.review_summary_wrap {
	display: block;
	font-size: 0;

	.gap {
		display: inline-block;
		width: 4px;
		height: 8px;
		margin: 6px 6px 0;
		@include mixins.ratio_images('../images/ico_slash', 'png', 4px, 8px);
		vertical-align: top;
	}

	.review_quotes_text {
		margin-right: 1px;

		& + .gap {
			margin: 6px 6px 0;
		}
	}

	.rating-container {
		&.rating-sm {
			margin-top: 3px;
		}
	}

	&.type_sm {
		.gap {
			margin: 6px 6px 0;

			& + .review_quotes_text {
				margin-top: 1px;
			}
		}

		.review_quotes_text {
			& + .gap {
				margin: 6px 6px 0 5px;
			}
		}

		.rating-container {
			&.rating-sm {
				margin-top: 3px;
			}
		}
	}
}

/* 할인쿠폰 */
.coupon_item {
	@include mixins.justify_width();
	align-items: stretch;
	position: relative;
	width: 340px;
	min-height: 153px;
	background: #fff;
	box-sizing: border-box;
	font-size: 0;

	&:before,
	&:after {
		content: '';
		position: absolute;
		right: 41px;
		z-index: 1;
		width: 17px;
		height: 6px;
		background: {
			repeat: no-repeat;
			position: center;
		}
	}

	&:before {
		top: 0;
		@include mixins.ratio_images('../images/ico_coupon_dent_top', 'png', 17px, 6px);
	}

	&:after {
		bottom: 0;
		@include mixins.ratio_images('../images/ico_coupon_dent_bottom', 'png', 17px, 6px);
	}

	.left_box {
		@include mixins.justify_width(start, center, column);
		flex: 1;
		flex-wrap: wrap;
		overflow: hidden;
		padding: 23px 25px 17px;
		background: #fff;
		border: 1px solid variables.$colorGray02;
		border-right: 0;
		border-radius: 10px 0 0 10px;
		box-sizing: border-box;

		.coupon_val {
			display: block;
			width: 100%;
			line-height: 1;
			word-break: break-all;

			.val {
				display: inline-block;
				color: variables.$textColorGreen;
				font-size: 34px;
				font-weight: variables.$fontWeightBold;
				vertical-align: top;

				.unit {
					color: variables.$textColorGreen;
					font-size: variables.$fontSizeMd;
					font-weight: variables.$fontWeightRegular;
					vertical-align: 2px;
				}
			}
		}

		.coupon_title {
			display: block;
			width: 100%;
			margin-top: 9px;
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
		}

		.coupon_info {
			display: block;
			width: 100%;
			margin-top: 8px;

			.coupon_info_list {
				.coupon_info_item {
					display: block;
					color: variables.$textColorLightGray;
					@include mixins.font_size_xxs;
				}
			}
		}
	}

	.right_box {
		display: inline-block;
		position: relative;
		flex: 0 50px;
		overflow: hidden;
		background: variables.$colorGray05;
		border: 1px solid variables.$colorGray02;
		border-left: 0;
		border-radius: 0 10px 10px 0;
		box-sizing: border-box;

		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 1px;
			height: calc(100% - 20px);
			margin: 10px 0;
			border-left: 1px dashed variables.$colorGray03;
			box-sizing: border-box;
		}

		.btn_coupon_down {
			display: inline-block;
			position: relative;
			width: 100%;
			height: 100%;
			background: {
				position: 15px center;
				repeat: no-repeat;
			}

			// 아이콘
			[class^=ico] {
				position: absolute;
				left: 50%;
				top: 50%;
				@include mixins.transform(translate(-50%, -50%));
			}

			.ico_down {
				width: 21px;
				height: 20px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/ico_coupon_down', 'png', 21px, 20px);
			}

			.ico_down_done {
				width: 17px;
				height: 16px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/ico_coupon_down_disabled', 'png', 17px, 16px);
			}

			&[disabled], &[aria-disabled], &.disabled {
				pointer-events: none;
				cursor: default;
			}
		}
	}

	&.disabled {
		opacity: 0.5;
	}
}
.coupon_list {
	.coupon_item {
		& ~ .coupon_item {
			margin-top: 10px;
		}
	}

	& + .bul_list,
	& + [class^=bul_item_] {
		margin-top: 11px;
	}
}

/* 말풍선 */
.bubble_speech_box {
	position: relative;

	.bubble_speech_desc {
		position: absolute;
		left: 50%;
		bottom: -38px;
		width: 221px;
		min-height: 30px;
		padding: 6px 5px 5px;
		border-radius: 15px;
		background-color: #f7f7fb;
		border: 1px solid #b8badf;
		@include mixins.font_size_min;
		color: variables.$textColorPurple;
		box-sizing: border-box;
		text-align: center;
		box-shadow: 0 5px 10px rgba(variables.$textColorBlack, 0.1);
		@include mixins.transform(translateX(-50%));

		&:before {
			content: '';
			position: absolute;
			top: -7px;
			left: 50%;
			display: block;
			width: 10px;
			height: 7px;
			margin-left: -5px;
			@include mixins.ratio_images('../images/bg_bubble_speech', 'png', 10px, 7px);
		}
	}
}



/* no data */
.no_data {
	width: 100%;
	padding: 180px 0;

	.no_data_desc {
		display: block;
		@include mixins.font_size_xs;
		color: variables.$textColorLightGray;
		text-align: center;

		&:before {
			content: '';
			display: block;
			width: 40px;
			height: 40px;
			margin: 0 auto 12px;
			@include mixins.ratio_images('../images/ico_nodata', 'png', 40px, 40px);
		}
	}

	&.size_sm {
		margin: 0;

		.no_data_desc {
			@include mixins.font_size_xxs;

			&:before {
				width: 30px;
				height: 30px;
				margin-bottom: 10px;
				@include mixins.ratio_images('../images/ico_nodata_sm', 'png', 30px, 30px);
			}
		}

	}
}

/* 리뷰 */
.all_review_wrap {
	.all_review_list {
		.all_review_item {
			border-bottom: 1px solid variables.$colorGray04;
			margin-top: 20px;
			padding: 0 10px 20px;
			box-sizing: border-box;
			position: relative;

			.review_middle {
				.review_middle_box {
					.auto_overflow_wrap {
						.auto_overflow_contents {
							max-height: 65px;

							.auto_overflow_inner {
								.info_text {
									margin-bottom: 13px;
									@include mixins.multi_ellipsis(22px, 3, false);
									color: variables.$textColorBlack;
								}
							}
						}

						&.active {
							.auto_overflow_contents {
								.auto_overflow_inner {
									.info_text {
										@include mixins.clear_multi_ellipsis;
									}
								}
							}
						}

						.auto_overflow_footer {
							.overflow_footer_inner {
								@include mixins.justify_width(center, between);

								.review_button_common {
									margin-top: 0;
								}
							}
						}
					}
				}
			}

			.review_top_common {
				@include mixins.justify_width(center, between);
				margin-bottom: 16px;
			}

			.review_button_common {
				@include mixins.justify_width(center);
				margin-top: 12px;

				.review_link {
					color: variables.$kyoboPurple;
					font-weight: variables.$fontWeightMedium;
					@include mixins.font_size_xs;
					flex: 1 0;

					&:after {
						content: '';
						display: inline-block;
						width: 5px;
						height: 9px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/arw_review_more', 'png', 5px, 9px);
						margin-left: 4px;
					}
				}

				.bottom_right {
					margin-left: auto;

					.btn_like {
						.ico_like {
							width: 20px;
							height: 20px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/ico_like', 'png', 20px, 20px);
							margin-right: 4px;
						}

						&.active {
							.ico_like {
								@include mixins.ratio_images('../images/ico_like_pre', 'png', 20px, 20px);
							}
						}

						.like_cont {
							position: relative;
							@include mixins.font_size_xxs;
							display: inline-block;
							vertical-align: middle;
							padding-top: 1px;
						}
					}

					.btn_reply {
						margin-left: 12px;

						&.active {
							.ico_reply {
								@include mixins.ratio_images('../images/ico_reply_pre', 'png', 20px, 20px);
							}

							.text_reply {
								color: variables.$textColorBlack;
							}
						}

						.ico_reply {
							width: 20px;
							height: 20px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/ico_reply', 'png', 20px, 20px);
							margin-right: 4px;
						}

						.text_reply {
							position: relative;
							@include mixins.font_size_xxs;
							color: variables.$textColorLightGray;
							display: inline-block;
							vertical-align: middle;
						}
					}
				}
			}

			//썸네일 + 스와이퍼 형
			&.thumbnail {
				.review_middle {

					.review_middle_box {
						.auto_overflow_wrap {
							.auto_overflow_contents {
								overflow: inherit;

								.auto_overflow_inner {
									@include mixins.justify_width(top, between);

									.info_text {
										flex: 1;
										margin-bottom: 12px;
										@include mixins.font_size_xs;
										@include mixins.multi_ellipsis(22px, 3, false);
										color: variables.$textColorBlack;
									}

									.review_thumb {
										position: relative;
										width: 64px;
										height: 64px;
										margin-left: 60px;

										.thumb_cont {
											position: absolute;
											display: inline-block;
											width: 16px;
											height: 16px;
											@include mixins.font_size_min;
											color: #fff;
											background-color: rgba(0, 0, 0, .85);
											bottom: 0;
											right: 0;
											text-align: center;
											border-radius: 6px 0 6px 0;
										}
									}
								}

								.review_swiper {
									display: none;
									position: relative;
									box-sizing: border-box;

									.swiper-container {

										.swiper-wrapper {
											.swiper-slide {
												width: 300px;
												height: 300px;
												margin-right: 20px;

												img {
													width: 100%;
												}
											}
										}
									}

									.swiper-button-next {
										right: -22px;
									}

									.swiper-button-prev {
										left: -22px;
									}
								}
							}

							&.active {
								.auto_overflow_contents {
									.auto_overflow_inner {
										display: block;

										.info_text {
											@include mixins.clear_multi_ellipsis;
										}

										.review_thumb {
											display: none;
										}
									}

									.review_swiper {
										display: block;
									}
								}
							}
						}
					}

					.review_middle_right {
						.review_thumb {
							position: relative;

							.thumb_cont {
								position: absolute;
								display: inline-block;
								width: 16px;
								height: 16px;
								@include mixins.font_size_min;
								color: #fff;
								background-color: rgba(0, 0, 0, .85);
								bottom: 0;
								right: 0;
								text-align: center;
								border-radius: 6px 0 6px 0;
							}
						}
					}
				}
			}

			//스포일러 형
			&.spoilers {

				.spoilers_text_box {
					width: 100%;
					padding: 19px 0;
					text-align: center;
					background-color: variables.$colorGray06;
					border-radius: 6px;
					margin-bottom: 12px;

					.text_spoilers {
						position: relative;
						display: inline-block;
						@include mixins.font_size_xs;
						color: variables.$textColorLightGray;
						padding-left: 25px;
						margin-top: 1px;

						&:before {
							content: '';
							display: inline-block;
							width: 17px;
							height: 19px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/ico_locker', 'png', 17px, 19px);
							position: absolute;
							top: 0;
							left: 0;
						}
					}
				}
			}

			//신고 형
			&.report_wrap {
				.review_middle {
					.report_text_box {
						height: 62px;
						background-color: variables.$colorGray06;
						border-radius: 6px;
						padding: 21px 0;
						text-align: center;
						box-sizing: border-box;
						margin-top: 16px;
						margin-bottom: 12px;

						.info_text {
							color: variables.$textColorLightGray;

							&:before {
								content: '';
								display: inline-block;
								width: 17px;
								height: 17px;
								@include mixins.ratio_images('../images/ico_ exclamation_gray_sm', 'png', 17px, 17px);
								vertical-align: middle;
								margin-right: 2px;
							}
						}
					}
				}
			}

			//댓글 형
			&.reply {
				.review_reply {
					display: block;
					padding-top: 12px;
					margin-top: 20px;
					border-top: 1px solid variables.$colorGray04;

					.byte_check_wrap {
						display: none;

						&.active {
							display: block;
						}
					}

					.reply_list {
						.reply_list_item {
							position: relative;
							padding: 20px 20px 24px 37px;
							box-sizing: border-box;
							background-color: variables.$colorGray06;
							margin-top: 8px;
							border-radius: 10px;

							.review_user_info {
								margin-bottom: 9px;

								&:before {
									content: '';
									display: inline-block;
									width: 8px;
									height: 9px;
									@include mixins.ratio_images('../images/arw_reply', 'png', 8px, 9px);
									position: absolute;
									top: 24px;
									left: 20px;
								}
							}
						}
					}

					.auto_overflow_wrap {
						.auto_overflow_contents {
							max-height: 0;
						}

						&.active {
							.auto_overflow_contents {
								max-height: none;
							}
						}
					}
				}
			}
		}
	}
}

/* 리뷰 - 리뷰 없는 경우 */
.no_review_wrap {
	.no_review_inner {
		text-align: center;
		position: relative;
		height: 179px;
		padding-top: 100px;
		border-bottom: 1px solid variables.$colorGray04;
