@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

// fold default
.fold_box_wrap {
	.fold_box_list {
		.fold_box {
			+ .fold_box {
				margin-top: 8px;
			}
		}
	}

	.fold_box {
		border: 1px solid variables.$colorGray03;
		border-radius: 10px;
		box-sizing: border-box;

		.fold_box_header, .fold_box_contents {
			padding: 0 20px;
		}

		.fold_box_header {
			position: relative;
			padding: 23px 30px 20px 23px;
			cursor: pointer;
			@include mixins.font_size_sm;
			font-weight: variables.$fontWeightMedium;

			&:after {
				content: '';
				position: absolute;
				display: block;
				width: 10px;
				height: 5px;
				right: 20px;
				top: 50%;
				@include mixins.ratio_images('../images/arw_fold_toggle', 'png', 10px, 5px);
				@include mixins.transform(translateY(-50%));
			}

			.btn_fold {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}

		.fold_box_contents {
			height: 0;
			overflow: hidden;
		}

		&.expanded {
			border-color: variables.$textColorBlack;

			.fold_box_header {
				&:after {
					@include mixins.transform(translateY(-50%) rotate(180deg));
				}
			}

			> .fold_box_contents {
				height: auto;
				padding: {
					top: 10px;
					bottom: 23px;
				}

				.form_wrap {
					+ .btn_wrap {
						margin-top: 20px;
					}
				}
			}
		}

		&.no_fold {
			.fold_box_header {
				&:after {
					content: none;
				}

				cursor: default;

				.btn_fold {
					pointer-events: none;
				}
			}
		}

		// fold select type 추가
		&.type_select {
			border-radius: 6px;
			background-color: #fff;

			.fold_box_header {
				padding: 11px 37px 11px 13px;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightRegular;

				&.type_prd {
					@include mixins.justify_width(center, start, row);
					box-sizing: border-box;
					width: 100%;
					padding: 10px 30px 10px 13px;
					text-align: left;

					.cover {
						width: 48px;
						height: 48px;
						border: 1px solid variables.$colorGray04;
						margin: 0 10px 0 0;
						box-sizing: border-box;
					}

					.info {
						width: calc(100% - 58px);
						padding-top: 2px;

						.flag {
							display: block;
							margin-bottom: 5px;
							color: variables.$textColorGray;
							font-size: variables.$fontSizeMin;
							letter-spacing: variables.$letterSpacing;
							line-height: 1;
							font-weight: variables.$fontWeightMedium;
						}

						.name {
							display: block;
							@include mixins.font_size_xs;
							font-size: variables.$fontSizeXs;
							letter-spacing: variables.$letterSpacing;
							line-height: 1;
							@include mixins.ellipsis;
						}
					}
				}
			}

			.fold_box_contents {
				padding: 0;
			}

			.fold_select_wrap {
				padding: 0 0 5px;
				border-top: 1px solid #000;

				.fold_select_list {
					.fold_select_item {
						@include mixins.justify_width(center, between, row);
						box-sizing: border-box;
						width: 100%;
						padding: 12px 13px 12px 11px;;
						text-align: left;
						cursor: pointer;

						.name {
						}

						.opt {
							flex-shrink: 0;
							padding-left: 10px;

							&.type_gray {
								color: variables.$textColorGray;
							}

							&.type_free {
								font-weight: variables.$fontWeightBold;
								color: variables.$textColorPurple;
							}
						}

						&:hover {
							background-color: #f5f5f5;
						}

						&:disabled {
							cursor: default;

							.name, .opt {
								color: variables.$textColorLightGray;
							}

							&:hover {
								background: none;
							}
						}
					}

					.fold_select_prd {
						@include mixins.justify_width(center, start, row);
						box-sizing: border-box;
						width: 100%;
						padding: 10px 13px;
						text-align: left;
						cursor: pointer;

						.cover {
							position: relative;
							width: 48px;
							height: 48px;
							border: 1px solid variables.$colorGray04;
							margin-right: 10px;
							box-sizing: border-box;
							background-color: #fff;
						}

						.info {
							position: relative;
							width: calc(100% - 58px);
							padding-top: 2px;

							.flag {
								display: block;
								margin-bottom: 5px;
								color: variables.$textColorGray;
								font-size: variables.$fontSizeMin;
								letter-spacing: variables.$letterSpacing;
								line-height: 1;
								font-weight: variables.$fontWeightMedium;
							}

							.name {
								display: block;
								@include mixins.font_size_xs;
								font-size: variables.$fontSizeXs;
								letter-spacing: variables.$letterSpacing;
								line-height: 1;
								@include mixins.ellipsis;
							}
						}

						&:hover {
							background-color: #f5f5f5;
						}

						&:disabled {
							cursor: default;

							.cover {
								opacity: .5;
							}

							.info {
								.flag, .name {
									color: variables.$textColorLightGray;
								}
							}

							&:hover {
								background-color: transparent;
							}
						}
					}
				}
			}

			&.type_floating {
				position: relative;
				border: none;

				.fold_box_header {
					border: 1px solid variables.$colorGray03;
					border-radius: 6px;
					@include mixins.transition(border-color .23s);
				}

				.fold_box_contents {
					position: absolute;
					top: 69px;
				}

				&.expanded {
					.fold_box_header {
						border-radius: 6px 6px 0 0;
						border-color: variables.$textColorBlack;
					}

					.fold_box_contents {
						width: 100%;
						max-height: 342px;
						border: 1px solid variables.$textColorBlack;
						background-color: #fff;
						border-radius: 0 0 6px 6px;
						box-sizing: border-box;
						z-index: 2;

						.fold_select_wrap {
							max-height: 342px;
							border-top: none;
						}
					}
				}
			}

			&.disabled {
				background-color: variables.$colorGray06;

				.fold_box_header {
					color: variables.$textColorLightGray;
					cursor: default;
				}

				.btn_fold {
					pointer-events: none;
					cursor: default;

					&[disabled] {
						pointer-events: none;
						cursor: default;
					}
				}
			}
		}
	}
}