@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

$inputProps: (
		'border-radius': 6px,
		'padding': 0 23px,
);

/* form 공통 */
.form_label {
	display: inline-block;
	color: variables.$textColorBlack;
	@include mixins.font_size_xxs;
	font-weight: variables.$fontWeightMedium;
	cursor: default;

	.required {
		display: inline-block;
		width: 6px;
		height: 6px;
		padding-left: 3px;
		margin-top: 5px;
		vertical-align: top;

		&:before {
			content: '*';
			display: inline-block;
			position: relative;
			width: 6px;
			height: 6px;
			@include mixins.ratio_images('../images/ico_required', 'png', 6px, 6px);
			color: transparent;
		}
	}

	.desc_gray {
		color: variables.$textColorLightGray;
	}
}

/* form 공통 - 하단 메시지 */
.form_desc {
	display: inline-block;
	position: relative;
	margin-top: 6px;
	@include mixins.font_size_xxs;
	color: variables.$textColorLightGray;

	&.tip {
		padding-left: 33px;

		&:before {
			content: '';
			display: inline-block;
			position: absolute;
			left: 0;
			top: 0;
			width: 28px;
			height: 18px;
			background: {
				position: left top;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_tip', 'png', 28px, 18px);
		}
	}
}

/* form - input */
[class^=form_] {
	&::-ms-clear,
	&::-ms-reveal, {
		display: none;
	}

	&::-webkit-search-decoration,
	&::-webkit-search-cancel-button,
	&::-webkit-search-results-button,
	&::-webkit-search-results-decoration {
		display: none;
	}

	&[type=number] {
		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}
	}

	&[type=date], &[type=month], &[type=time], &[type=week] {
		&::-webkit-calendar-picker-indicator {
			background: none;
		}
	}

	&[type=search] {
		&::-webkit-search-cancel-button,
		&::-webkit-search-results-button,
		&::-webkit-search-results-decoration {
			display: none;
			width: 0;
			height: 0;
		}
	}
}
.form_ip {
	display: inline-block;
	width: 100%;
	height: 44px;
	padding: 0 14px 2px;
	color: variables.$textColorBlack;
	font-size: variables.$fontSizeXs;
	line-height: 42px;
	letter-spacing: variables.$letterSpacing;
	font-family: variables.$fontKor;
	background-color: #fff;
	border: 1px solid variables.$colorGray03;
	border-radius: 6px;
	box-sizing: border-box;
	@include mixins.transition(border-color .2s ease-out);
	outline: none;

	&:active, &:focus {
		border-color: #000;
	}

	&[disabled], &[aria-disabled], &[readonly] {
		color: variables.$textColorLightGray;
		background-color: variables.$colorGray06;
		pointer-events: none;
		cursor: default;
	}

	@include mixins.set_placeholder_color(variables.$textColorLightGray);

	// 여백
	& ~ & {
		margin-top: 8px;
	}

	& ~ .form_box, & ~ .form_item {
		margin-top: 20px;
	}
}

/* input password 숨김/보임 */
.form_ip_pw {
	position: relative;

	.form_ip {
		padding: 0 48px 0 14px;
	}

	.btn_toggle_pw {
		display: inline-block;
		position: absolute;
		right: 14px;
		top: 11px;
		width: 22px;
		height: 20px;
		@include mixins.ratio_images('../images/ico_eye', 'png', 22px, 20px);
		@include mixins.transition(background 0.2s ease-out);

		&.active {
			@include mixins.ratio_images('../images/ico_eye_active', 'png', 22px, 20px);
		}
	}

	& ~ & {
		margin-top: 8px;
	}
}

/* input - 검색 */
.form_ip_search {
	position: relative;

	.btn_ip_clear {
		display: none;
		position: absolute;
		right: 14px;
		top: 50%;
		width: 18px;
		height: 18px;
		@include mixins.transform(translateY(-50%));
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/btn_ip_reset', 'png', 18px, 18px);
	}

	&.value {
		.form_ip {
			padding-right: 85px;
		}

		.btn_ip_clear {
			display: block;
		}
	}
}
.form_search_box {
	@include mixins.justify_width(center);
	position: relative;
	width: 100%;

	.form_ip_search {
		flex: 1;

		.btn_ip_clear {
			right: 50px;
		}
	}

	.btn_ip_search {
		display: block;
		flex: 0 auto;
		position: absolute;
		right: 14px;
		top: 50%;
		width: 24px;
		height: 24px;
		transform: translateY(-50%);
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/btn_ip_search', 'png', 24px, 24px);
	}
}

.form_prefix_box {
	position: relative;

	.prefix_page_unit {
		position: absolute;
		left: 14px;
		top: 50%;
		padding-top: 1px;
		color: variables.$textColorBlack;
		@include mixins.font_size_xs;
		@include mixins.transform(translateY(-50%));
	}

	.form_ip {
		padding: 0 14px 2px 28px;
	}
}

/* select */
// select button 영역
.form_sel {
	display: inline-block;

	.ui-selectmenu-button {
		display: inline-block;
		position: relative;
		width: 400px;
		height: 44px;
		margin: 0;
		padding: 0 14px;
		background-color: #fff;
		border: 1px solid variables.$colorGray03;
		border-radius: 6px;
		box-sizing: border-box;
		@include mixins.transition(border-color .2s ease-out, border-radius .2s ease-out);
		outline: none;

		&:active, &:focus {
			border-color: #000;
		}

		.ui-selectmenu-text {
			font-size: variables.$fontSizeXs;
			line-height: 42px;
			letter-spacing: variables.$letterSpacing;
			color: variables.$textColorBlack;
			font-family: variables.$fontKor;
		}

		.ui-selectmenu-icon {
			position: absolute;
			right: 14px;
			top: 17px;
			width: 9px;
			height: 8px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/arw_select', 'png', 9px, 8px);
			@include mixins.transform(rotate(0));
			@include mixins.transition(transform 0.2s ease-out);
		}

		&.ui-selectmenu-button-open {
			border-color: #000;
			border-radius: 6px 6px 0 0;

			.ui-selectmenu-icon {
				@include mixins.transform(rotate(180deg));
			}
		}

		&.ui-selectmenu-disabled {
			background-color: variables.$colorGray06;

			.ui-selectmenu-text {
				color: variables.$textColorLightGray;
			}
		}
	}

	// select - sm type button
	&.type_sm {
		.ui-selectmenu-button {
			width: 120px;
			height: 38px;

			.ui-selectmenu-text {
				font-size: variables.$fontSizeXxs;
				line-height: 36px;
			}

			.ui-selectmenu-icon {
				top: 14px;
			}
		}
	}

	// select - arw type button
	&.type_arw {
		.ui-selectmenu-button {
			width: auto;
			height: 19px;
			padding: 0;
			border: none;

			.ui-selectmenu-text {
				display: inline-block;
				margin-right: 23px;
				@include mixins.font_size_xxs;
			}

			.ui-selectmenu-icon {
				top: 0;
				right: 0;
				width: 19px;
				height: 19px;
				border: 1px solid variables.$colorGray03;
				border-radius: 50%;
				box-sizing: border-box;
				background: {
					position: center 7px;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/arw_select_sm', 'png', 7px, 4px);
				@include mixins.transition(transform 0.2s ease-out, border-color 0.2s ease-out);
			}

			&.ui-selectmenu-button-open {
				.ui-selectmenu-icon {
					border-color: #000;
				}
			}

			// TODO : 디자인팀 문의 후 수정 필요
			&.ui-selectmenu-disabled {
				background-color: variables.$colorGray06;

				.ui-selectmenu-text {
					color: variables.$textColorLightGray;
				}
			}
		}

		& ~ &.type_arw {
			margin-left: 12px;
		}
	}

	// select - width full type
	&.w_full {
		width: 100%;

		.ui-selectmenu-button {
			width: 100%;
		}
	}
}

/* radio/check */
.form_rdo, .form_chk {
	display: inline-block;
	position: relative;
	min-height: 22px;
	vertical-align: top;

	input {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;

		+ label {
			display: inline-block;
			position: relative;
			padding: 2px 0 0 27px;
			font-size: variables.$fontSizeXs;
			letter-spacing: variables.$letterSpacing;
			line-height: 20px;
			color: variables.$textColorBlack;
			cursor: pointer;
			vertical-align: top;
			@include mixins.transition(color .2s ease-out);
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;

			&:before,
			&:after {
				content: '';
				display: inline-block;
				position: absolute;
				left: 0;
				top: 0;
				width: 22px;
				height: 22px;
				text-align: center;
				border-radius: 50%;
				box-sizing: border-box;
			}

			.desc_gray {
				color: variables.$textColorLightGray;
			}
		}

		&[disabled], &[aria-disabled], &[readonly] {
			& + label {
				color: variables.$textColorLightGray;
				pointer-events: none;

				&:before {
					background-color: variables.$colorGray06;
					border-color: variables.$colorGray04;
				}
			}
		}

		&:checked {
			& + label {
				&:before {
					background-color: variables.$kyoboPurple;
					border-color: variables.$kyoboPurple;
				}
			}

			&[disabled], &[aria-disabled], &[readonly] {
				& + label {
					&:before {
						background-color: #b8badf;
						border-color: #b8badf;
					}
				}
			}
		}
	}

	&.no_label {
		width: 22px;
		height: 22px;
		min-height: auto;

		input {
			+ label {
				padding-left: 0;
				font-size: 0;
			}
		}
	}
}

.form_chk {
	input {
		+ label {
			&:before {
				background-color: #fff;
				border: 1px solid variables.$colorGray02;
				@include mixins.transition(border-color 0.2s ease-out, background-color 0.2s ease-out);
			}

			&:after {
				background: {
					position: center 8px;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/ico_checkbox', 'png', 10px, 7px);
			}
		}

		&:checked {
			& + label {
				&:after {
					@include mixins.ratio_images('../images/ico_checkbox_active', 'png', 10px, 7px);
				}
			}
		}

		&[disabled], &[aria-disabled], &[readonly] {
			& + label {
				&:after {
					@include mixins.ratio_images('../images/ico_checkbox_disabled', 'png', 10px, 7px);
				}
			}
		}
	}
}

.form_rdo {
	input {
		+ label {
			&:before {
				background-color: #fff;
				border: 1px solid variables.$colorGray02;
				@include mixins.transition(border-color 0.2s ease-out, background-color 0.2s ease-out);
			}

			&:after {
				content: '';
				position: absolute;
				left: 6px;
				top: 6px;
				width: 10px;
				height: 10px;
				background-color: variables.$colorGray02;
				@include mixins.transition(background 0.2s ease-out);
			}
		}

		&:checked {
			& + label {
				&:after {
					background-color: #fff;
				}
			}
		}

		&[disabled], &[aria-disabled], &[readonly] {
			& + label {
				&:after {
					background-color: variables.$colorGray04;
				}
			}
		}
	}
}

.form_filter_box {
	display: inline-block;
	font-size: 0;

	.form_chk_filter {
		input {
			position: absolute;
			width: 1px;
			height: 1px;
			padding: 0;
			margin: -1px;
			overflow: hidden;
			clip: rect(0, 0, 0, 0);
			border: 0;

			+ label {
				display: inline-block;
				position: relative;
				z-index: 1;
				min-width: 88px;
				height: 44px;
				padding: 0 16px;
				color: variables.$textColorLightGray;
				font-size: variables.$fontSizeXs;
				line-height: 42px;
				letter-spacing: variables.$letterSpacing;
				font-family: variables.$fontKor;
				background-color: #fff;
				border: 1px solid variables.$colorGray03;
				box-sizing: border-box;
				text-align: center;
				cursor: pointer;
				vertical-align: top;
				@include mixins.transition(border-color 0.2s ease-out);
				-webkit-user-select: none;
				-moz-user-select: none;
				-ms-user-select: none;

				.text {
					position: relative;
					padding-left: 14px;

					&:before {
						content: '';
						position: absolute;
						left: 0;
						top: 50%;
						width: 10px;
						height: 7px;
						margin-top: -3px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/ico_checkbox', 'png', 10px, 7px);
					}
				}
			}

			&:checked {
				& + label {
					z-index: 2;
					border-color: variables.$kyoboPurple;
					color: variables.$textColorPurple;

					.text {
						&:before {
							@include mixins.ratio_images('../images/ico_checkbox_active_02', 'png', 10px, 7px);
						}
					}
				}
			}
		}

		&:first-child {
			input + label {
				border-radius: 6px 0 0 6px;
			}
		}

		&:last-child {
			input + label {
				border-radius: 0 6px 6px 0;
			}
		}

		&:only-child {
			input + label {
				border-radius: 6px;
			}
		}

		& ~ .form_chk_filter {
			input + label {
				margin-left: -1px;
			}
		}
	}
}

/* checkbox & radio row list */
.chk_row_list {
	font-size: 0;

	.chk_row_item {
		display: inline-block;
		margin-right: 24px;

		&:last-child {
			margin-right: 0;
		}
	}
}

/* checkbox & radio col list */
.chk_col_list {
	.chk_col_item {
		& ~ .chk_col_item {
			margin-top: 16px;

			.chk_col_list {
				margin: 14px 0 0 27px;
			}
		}
	}
}

/* textarea */
.form_textarea {
	width: 100%;
	height: 150px;
	padding: 12px 14px;
	color: variables.$textColorBlack;
	border: 1px solid variables.$colorGray03;
	border-radius: 6px;
	@include mixins.font_size_xs;
	font-family: variables.$fontKor;
	background-color: #fff;
	box-sizing: border-box;
	@include mixins.transition(background 0.2s ease-out, border-color .2s ease-out);
	outline: 0;
	overflow-y: auto;
	resize: none;

	&:active, &:focus {
		border-color: #000;
	}

	&[disabled], &[aria-disabled], &[readonly] {
		color: variables.$textColorLightGray;
		background-color: variables.$colorGray06;
		pointer-events: none;
		cursor: default;
	}

	@include mixins.set_placeholder_color(variables.$textColorLightGray);
}
/* byte_check */
.byte_check_wrap {
	display: flex;
	flex-direction: column;
	overflow: hidden;
	border: 1px solid variables.$colorGray03;
	border-radius: 6px;
	font-size: 0;
	background: #fff;

	.form_textarea {
		border: none;
		padding: 11px 14px 0;

		&[disabled], &[aria-disabled], &[readonly] {
			& + .byte_chack_footer {
				color: variables.$textColorLightGray;
				background-color: variables.$colorGray06;
			}
		}
	}

	.byte_chack_footer {
		@include mixins.justify_width(center, end);
		padding: 10px 10px 10px 14px;
		text-align: right;
		@include mixins.transition(background 0.2s ease-out);

		.byte_check {
			display: inline-block;
			margin-top: 2px;
			font-size: 0;

			.count, .total {
				display: inline-block;
				@include mixins.font_size_min;
			}

			.count {
				display: inline-block;
				color: variables.$textColorDarkGray;
			}

			.total {
				color: variables.$textColorLightGray;

				&:before {
					content: '/';
				}
			}
		}

		.btn_wrap {
			display: inline-block;

			[class^=btn_] + [class^=btn_] {
				margin-left: 4px;
			}
		}

		.byte_check + .btn_wrap {
			margin-left: 10px;
		}
	}
}

/* form - 유효성 검사 */
.valid_check {
	.valid_desc {
		display: none;
		position: relative;
		margin-top: 4px;
		@include mixins.font_size_xxs;
		color: variables.$textColorLightGray;
	}

	.form_sel ~ .valid_desc {
		display: block;
	}

	&.success {
		.form_ip {
			border: 1px solid variables.$kyoboGreen;
		}

		.form_sel {
			.ui-selectmenu-button {
				border: 1px solid variables.$kyoboGreen;
			}
		}

		.valid_desc {
			display: block;
			color: variables.$textColorGreen;
		}
	}

	&.fail {
		.form_ip {
			border: 1px solid variables.$textColorRed;
		}

		.form_sel {
			.ui-selectmenu-button {
				border: 1px solid variables.$textColorRed;
			}
		}

		.valid_desc {
			display: block;
			padding-left: 16px;
			color: variables.$textColorRed;

			&:before {
				content: '';
				display: inline-block;
				position: absolute;
				left: 0;
				top: 3px;
				width: 12px;
				height: 12px;
				background: {
					position: left top;
				}
				@include mixins.ratio_images('../images/ico_warning', 'png', 12px, 12px);
			}
		}
	}

	// 여백
	& ~ & {
		margin-top: 20px;
	}
}

/* input - flex box */
.input_flex_box {
	@include mixins.justify_width(center);

	.gap {
		display: inline-block;
		margin: 0 10px;
		vertical-align: top;
	}

	.datepicker_box {
		flex: 0 auto;
	}

	.form_ip {
		flex: 0 auto;
		margin-top: 0;
	}
}

/* form - title(label) */
.form_wrap {
	.form_box {
		.form_title {
			.info_text {
				margin: 7px 0 2px;
			}
		}

		.form_cont {
			margin-top: 7px;
			color: variables.$textColorGray;

			.btn_text_link {
				color: inherit;
				font-size: inherit;
				line-height: inherit;
			}

			.byte_check_wrap {
				+ .form_chk {
					margin-top: 10px;
				}
			}

			.file_list, .file_list_box {
				+ .bul_item_asterisk {
					margin-top: 10px;
				}
			}
		}

		& ~ .form_box {
			margin-top: 20px;
		}
	}

	&.type_md {
		.form_box {
			.form_title {
				.form_label {
					@include mixins.font_size_xs;
				}

				.file_attach_val {
					padding-top: 4px;
				}
			}

			.form_cont {
				margin-top: 10px;

				.byte_check_wrap {
					+ .form_chk {
						margin-top: 10px;
					}
				}
			}

			& ~ .form_box {
				margin-top: 24px;
			}
		}
	}

	&.type_lg {
		.form_box {
			.form_title {
				.form_label {
					@include mixins.font_size_sm;
				}

				.file_attach_val {
					padding-top: 4px;
				}
			}

			.form_cont {
				margin-top: 10px;

				.byte_check_wrap {
					+ .form_chk {
						margin-top: 10px;
					}
				}
			}

			& ~ .form_box {
				margin-top: 24px;
			}
		}
	}
}

/* form - 첨부파일 */
.file_list {
	font-size: 0;

	.list_item {
		display: inline-block;
		margin-right: 10px;
		vertical-align: top;

		&:last-child {
			margin-right: 0;
		}
	}

	.file_item {
		position: relative;
		display: inline-block;

		.btn_box {
			position: relative;
			display: inline-block;
			width: 94px;
			height: 94px;
			border: 1px dashed #e5e5e5;
			cursor: pointer;
			border-radius: 6px;
			vertical-align: top;

			input[type=file] {
				/* 파일 필드 숨기기 */
				position: absolute;
				width: 1px;
				height: 1px;
				padding: 0;
				margin: -1px;
				overflow: hidden;
				clip: rect(0, 0, 0, 0);
				border: 0;
			}

			label {
				display: block;
				width: 100%;
				height: 100%;
				font-size: 0 !important;
				@include mixins.ratio_images('../images/btn_attached_add', 'png', 16px, 16px);
				background: {
					position: center;
					repeat: no-repeat;
				}
			}
		}

		&.attached {
			.btn_box {
				border: none;
			}

			label {
				display: none;
			}

			.attach_img_box {
				position: absolute;
				display: none;
				width: 100%;
				height: 100%;
				cursor: default;
				left: 0;
				top: 0;
				z-index: 1;

				.attach_img_view {
					position: absolute;
					display: inline-block;
					width: 100%;
					height: 100%;
					background: {
						position: center;
						size: cover;
					}
					font-size: 0 !important;
					left: 0;
					top: 0;
				}

				.btn_remove_img {
					position: absolute;
					width: 22px;
					height: 22px;
					@include mixins.ratio_images('../images/btn_attached_remove', 'png', 8px, 8px);
					background: {
						position: center;
						repeat: no-repeat;
					}
					background-color: rgba(variables.$textColorBlack, .6);
					border-radius: 0 6px;
					font-size: 0 !important;
					pointer-events: auto;
					right: 0;
					top: 0;
				}
			}

			&.attached {
				.attach_img_box {
					display: inline-block;
				}
			}
		}
	}

	~ .bul_item_asterisk {
		margin-top: 10px;
	}
}

.file_attach_val {
	display: inline-block;
	font-size: 0;
	vertical-align: top;
	line-height: 1;

	.val {
		font-size: variables.$fontSizeXs;
		letter-spacing: variables.$letterSpacing;
	}

	.total {
		font-size: variables.$fontSizeXs;
		letter-spacing: variables.$letterSpacing;
		color: variables.$textColorLightGray;
	}
}

.form_auto_complete_wrap {
	position: relative;

	.auto_complete_box {
		display: none;
		position: absolute;
		top: 44px;
		left: 0;
		width: 100%;
		height: 100px;
		border: 1px solid variables.$colorGray03;
		border-top: none;
		border-radius: 0 0 6px 6px;
		background-color: #fff;
		z-index: 2;

		.auto_complete_list {
			.auto_complete_item {
				.btn_select {
					display: block;
					width: 100%;
					padding: 10px 14px 9px;
					color: #000;
					@include mixins.font_size_xs;
					font-family: variables.$fontKor;
					box-sizing: border-box;
					@include mixins.transition(background-color 0.2s ease-out);
					text-align: left;

					&:hover {
						background-color: #f5f5f5;
					}
				}
			}
		}
	}
}

.form_col_group {
	.col_box {
		~ .col_box {
			margin-top: 8px;
		}
	}
}

.input_btn_box {
	@include mixins.justify_width;
	width: 100%;

	.form_ip, .time_limit {
		flex: 1;

		+ .btn_ip {
			margin-left: 5px;
		}
	}
}

.time_limit {
	position: relative;

	.form_ip {
		padding-right: 58px;
	}

	.time_count {
		position: absolute;
		right: 14px;
		top: 12px;
		@include mixins.font_size_xxs;
		color: variables.$textColorRed;
	}
}