@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* Table Row Wrap */
.tbl_row_wrap {
	display: block;
	width: 100%;
	padding: 18px 0 28px;
	border-top: 1px solid variables.$textColorBlack;
	border-bottom: 1px solid variables.$colorGray04;
	box-sizing: border-box;

	.tbl_row {
		table-layout: fixed;

		& + .tbl_row {
			margin-top: 18px;
			border-top: 1px solid variables.$colorGray04;

			tbody {
				tr {
					&:first-child {
						th, td {
							padding-top: 30px;
						}
					}
				}
			}
		}
	}

	/* Table Row Type */
	.tbl_row {
		width: 100%;

		tbody {
			th {
				padding: 12px 10px;
				@include mixins.font_size_xs;
				text-align: left;
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightMedium;
				vertical-align: top;
			}

			td {
				padding: 12px 10px;
				@include mixins.font_size_xs;
				text-align: left;
				color: variables.$textColorLightGray;
				font-weight: variables.$fontWeightRegular;
				vertical-align: top;

				.btn_xxs {
					margin: -2px 0 0 6px;
				}

				.btn_text_link {
					overflow: hidden;
					color: inherit;

					.text {
						text-decoration: underline;
					}
				}
			}
		}
	}
}

/* Table Column Wrap */
.tbl_col_wrap {
	display: block;
	width: 100%;
	border-top: 1px solid variables.$textColorBlack;
	border-bottom: 1px solid variables.$colorGray04;
	box-sizing: border-box;

	/* Table Column Type */
	.tbl_col {
		width: 100%;
		table-layout: fixed;

		tr {
			td {
				padding: 12px 10px;
				text-align: center;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightRegular;
				vertical-align: middle;

				.btn_text_link {
					color: inherit;

					.text {
						text-decoration: underline;
					}
				}

				&.align_left {
					text-align: left;
				}

				&.align_right {
					text-align: right;
				}
			}

			th {
				padding: 16px 10px 14px;
				text-align: center;
				@include mixins.font_size_xs;
				color: variables.$textColorLightGray;
				font-weight: variables.$fontWeightMedium;
				vertical-align: middle;
			}
		}
	}

	&:not(.type_th_gray) {
		.tbl_col {
			tbody {
				tr {
					&:first-child {
						border-top: 1px solid variables.$colorGray03;

						td {
							padding-top: 20px;
						}
					}

					&:last-child {
						td {
							padding-bottom: 30px;
						}
					}
				}
			}
		}
	}

	&.type_th_gray {
		.tbl_col {
			tr {
				th {
					background-color: variables.$colorGray06;
					border-bottom: 1px solid variables.$colorGray03;
				}

				td {
					padding: 16px 10px 18px;
					border-top: 1px solid variables.$colorGray04; // TODO : 임의작업. 추후 케이스 나오면 케이스 맞춰 수정
				}

				~ tr {
					th {
						border-top: 1px solid variables.$colorGray04;
					}
				}
			}
		}
	}
}
