@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* tab */
.tab_wrap {
	position: relative;
	width: 100%;
	padding: 0;

	// tab - tab_list_wrap
	> .tab_list_wrap {
		@include mixins.justify_width(center);
		width: 100%;
		height: 72px;
		margin-bottom: 30px; // TODO : 여백 가이드 나오면 수정
		background-color: #fff;
		border-bottom: 1px solid variables.$colorGray03;
		box-sizing: border-box;
		font-size: 0;

		.tabs {
			display: inline-block;
			flex: 1;
			position: relative;
			height: 100%;
			padding: 0;
			margin: 0;
			background-color: #fff;
			box-sizing: border-box;

			.tab_item {
				position: relative;
				height: 100%;
				margin: 0;
				padding: 0 30px;
				float: left;
				background: none;
				box-sizing: border-box;

				&:first-child {
					padding: 0 30px 0 0;
				}

				&:last-child {
					padding: 0 0 0 30px;
				}

				.tab_link {
					position: relative;
					@include mixins.justify_width(center, center, row);
					flex-wrap: wrap;
					width: 100%;
					height: 100%;
					padding: 0;
					text-align: center;
					box-sizing: border-box;
					cursor: pointer;

					&:after {
						content: '';
						position: absolute;
						left: 0;
						bottom: -1px;
						width: 100%;
						height: 3px;
						background-color: transparent;
						@include mixins.transition(background-color .3s ease-out);
					}

					.tab_text {
						position: relative;
						display: block;
						width: 100%;
						color: variables.$textColorLightGray;
						@include mixins.font_size_def;
						font-weight: variables.$fontWeightRegular;
						font-family: variables.$fontKor;
						box-sizing: border-box;
						@include mixins.transition(all .3s ease-out);
					}
				}

				&.ui-state-disabled {
					.tab_link {
						cursor: default;

						.tab_text {
							color: variables.$colorGray02; // TODO : 디자인 나오면 적용 필요
						}
					}
				}

				&.ui-state-active, &.active {
					z-index: 2;
					margin-bottom: 0;
					padding-bottom: 0;

					.tab_link {
						cursor: pointer;

						&:after {
							background-color: #000;
						}

						.tab_text {
							color: variables.$textColorBlack;
							font-weight: variables.$fontWeightBold;
						}
					}
				}
			}
		}

		.right_area {
			display: inline-block;
			flex: 0 auto;
			align-self: flex-start;
			margin-left: 30px;
		}
	}

	// tab - content
	.tab_content {
		padding: 0;
		background: #fff;
		box-sizing: border-box;
		outline: none;
	}
}

/* tab - type md 타입 */
.tab_wrap.type_md {
	> .tab_list_wrap {
		align-items: start;
		height: 50px;

		> .tabs {
			padding-bottom: 12px;

			.tab_item {
				padding: 0 20px;

				&:first-child {
					padding-left: 0;
				}

				&:last-child {
					padding-right: 0;
				}

				.tab_link {
					&:after {
						bottom: -13px;
						height: 2px;
					}

					.tab_text {
						@include mixins.font_size_xs;
					}
				}

				&.ui-state-active {
					.tab_link {
						&:after {
							background-color: variables.$kyoboPurple;
						}

						.tab_text {
							color: variables.$kyoboPurple;
						}
					}
				}
			}
		}
	}
}

/* tab - sm 타입 */
.tab_wrap.type_sm {
	> .tab_list_wrap {
		height: 44px;

		> .tabs {
			padding-bottom: 5px;

			.tab_item {
				padding: 0 12px;

				&:first-child {
					padding-left: 0;
				}

				&:last-child {
					padding-right: 0;
				}

				.tab_link {
					&:after {
						bottom: -6px;
						height: 2px;
					}

					.tab_text {
						@include mixins.font_size_xs;
					}
				}
			}
		}
	}
}

/* tab - line 타입 */
.tab_wrap.type_line {
	> .tab_list_wrap {
		height: 44px;
		margin-bottom: 40px;
		border-bottom: 1px solid #000;

		> .tabs {
			.tab_item {
				min-width: 160px;
				padding: 0;
				background: #fff;
				border: 1px solid variables.$colorGray03;
				border-bottom: 0;
				border-radius: 6px 6px 0 0;
				@include mixins.transition(none);

				.tab_link {
					padding: 0 14px;

					.tab_text {
						padding-bottom: 2px;
						@include mixins.font_size_xs;
					}

					&:after {
						bottom: -2px;
						@include mixins.transition(none);
					}
				}

				&.ui-state-active {
					border: 1px solid #000;
					border-bottom: 0;

					.tab_link {
						.tab_text {
							color: variables.$textColorBlack;
							font-weight: variables.$fontWeightMedium;
						}

						&:after {
							bottom: -2px;
							background: #fff;
						}
					}
				}

				& ~ .tab_item {
					margin-left: -1px;
				}
			}
		}
	}
}

/* tab - line primary 타입 */
.tab_wrap.type_line_primary {
	> .tab_list_wrap {
		height: 44px;
		border-bottom: 2px solid variables.$kyoboPurple;

		> .tabs {
			.tab_item {
				padding: 0;
				background: #fff;
				border: 1px solid variables.$colorGray03;
				border-bottom: 0;
				border-radius: 6px 6px 0 0;
				@include mixins.transition(none);

				.tab_link {
					padding: 0 14px;

					.tab_text {
						padding-bottom: 2px;
						@include mixins.font_size_xs;
					}

					&:after {
						bottom: -2px;
						@include mixins.transition(none);
					}
				}

				&.ui-state-active {
					border: 2px solid variables.$kyoboPurple;
					border-bottom: 0;

					.tab_link {
						.tab_text {
							color: variables.$textColorPurple;
						}

						&:after {
							bottom: -2px;
							background: #fff;
						}
					}
				}

				& ~ .tab_item {
					margin-left: -1px;
				}
			}
		}
	}
}

/* tab - text 타입 */
.tab_wrap.type_text {
	> .tab_list_wrap {
		height: 44px; // TODO : 높이 값 정해지면 수정 필요
		border-bottom: none;

		> .tabs {
			.tab_item {
				padding: 0 11px 0 12px;

				&:first-child {
					padding-left: 0;
				}

				&:last-child {
					padding-right: 0;
				}

				.tab_link {
					&:after {
						display: none;
					}

					.tab_text {
						@include mixins.font_size_xs;
					}
				}

				& ~ .tab_item {
					position: relative;

					&:after {
						content: '';
						display: block;
						position: absolute;
						left: 0;
						top: 50%;
						width: 1px;
						height: 10px;
						margin-top: 1px;
						background: variables.$colorGray04;
						@include mixins.transform(translateY(-50%));
					}
				}

				&.ui-state-active {
					.tab_link {
						.tab_text {
							color: variables.$kyoboPurple;
						}
					}
				}
			}
		}
	}
}

/* tab - 태그 타입 */
.tab_wrap.type_tag {
	> .tab_list_wrap {
		height: auto;
		min-height: 36px;
		margin-bottom: 24px; // TODO : 여백 가이드 나오면 수정
		border-bottom: 0;

		> .tabs {
			.tab_item {
				height: 36px;
				padding: 0;
				margin: 0 6px 8px 0;
				background: variables.$colorGray06;
				border-radius: 36px;
				@include mixins.transition(background .3s ease-out);

				.tab_link {
					padding: 8px 16px 9px;

					.tab_text {
						padding-bottom: 0;
						color: variables.$textColorGray;
						@include mixins.font_size_xxs;
					}

					&:after {
						display: none;
					}
				}

				&.ui-state-active {
					background: variables.$textColorGray;

					.tab_link {
						.tab_text {
							color: #fff;
							font-weight: variables.$fontWeightRegular;
						}
					}
				}
			}
		}
	}
}

/* tab - fill 타입 */
.tab_wrap.type_fill {
	> .tab_list_wrap {
		height: 54px;
		border-bottom: none;
		margin-bottom: 50px;

		> .tabs {
			> .tab_item {
				border: 1px solid variables.$colorGray03;
				background-color: #fff;
				padding: 0;
				@include mixins.transition(background .3s ease-out, border-color .3s ease-out);

				.tab_link {
					padding: 0 14px;

					.tab_text {
						padding-bottom: 1px;
						@include mixins.font_size_sm;
					}

					&:after {
						display: none;
					}
				}

				&.ui-state-active {
					background-color: variables.$kyoboPurple;
					border-color: variables.$kyoboPurple;

					.tab_text {
						color: #fff;
					}
				}

				& ~ .tab_item {
					margin-left: -1px;
				}

				&:first-child {
					border-radius: 6px 0 0 6px;
				}

				&:last-child {
					border-radius: 0 6px 6px 0;
				}
			}
		}
	}
}

/* tab - fill round 타입 - 회원(로그인) 사용중 */
.tab_wrap.type_fill_round {
	> .tab_list_wrap {
		height: 48px;
		border-bottom: none;
		margin-bottom: 35px;

		> .tabs {
			.tab_item {
				border: 1px solid variables.$colorGray03;
				border-bottom-color: variables.$kyoboPurple;
				border-radius: 10px 10px 0 0;
				background-color: variables.$colorGray06;
				padding: 0;
				@include mixins.transition(background .3s ease-out, border-color .3s ease-out);

				.tab_link {
					padding: 0 14px; // TODO :: 추후 디자인 나오면 좌우 여백 값 수정 필요

					.tab_text {
						padding-bottom: 1px;
						@include mixins.font_size_xs;
					}

					&:after {
						display: none;
					}
				}

				&.ui-state-active {
					background-color: variables.$kyoboPurple;
					border-color: variables.$kyoboPurple;

					.tab_text {
						color: #fff;
					}
				}

				& ~ .tab_item {
					margin-left: -1px;
				}
			}
		}
	}
}

/* tab - 균등형 */
.tab_wrap.justify {
	> .tab_list_wrap {
		> .tabs {
			@include mixins.justify_width(center, center, row);
			flex-wrap: wrap;
			width: 100%;

			.tab_item {
				-webkit-box-flex: 1;
				-ms-flex: 1;
				flex: 1;
				padding: 0;

				.tab_link {
					padding: 0 14px; // TODO :: 추후 디자인 나오면 좌우 여백 값 수정 필요
				}
			}
		}
	}

	&.type_tag {
		> .tab_list_wrap {
			> .tabs {
				.tab_item {
					margin: 0;

					& ~ .tab_item {
						margin-left: 6px;
					}
				}
			}
		}
	}
}